import { FC, useEffect, useState } from 'react';
import { COLOR_SETS } from '../../../contants/colors';
import { client } from '../../../App';
import { GET_AUTOMATED_TABLE } from '../../../queries/widgets';
import { ITheme } from '.';
import { generateGradientColors } from '../../../utils/generateColorSets';

type Props = {
  color: string;
  content: {
    title: string;
    subtitle: string;
    isDynamic?: boolean;
    countOfRow?: string | number;
    colors?: string[];
    data: {
      value: number | any;
      label: string | any;
    }[];
  };
  theme?: ITheme;
};

export const Blocks: FC<Props> = ({ color, content, theme }) => {
  const colors = theme ? (theme?.colorSet.gradientColors || generateGradientColors(theme?.mainColor, 8)) : COLOR_SETS[color.toLowerCase()] || COLOR_SETS['#ccd5df'];
  const [automaticTableData, setAutomaticTableData] = useState<any[]>([]);

  const fetchAutomatedTable = async (type: string, fields: string[], limit: string) =>
    client.query({
      query: GET_AUTOMATED_TABLE,
      variables: { param: type, fields, limit },
    });

  useEffect(() => {
    if (content?.isDynamic) {
      const fields = Object.values(content.data[0]).map(
        (item: any) =>
          item.fields && {
            field: item.fields[0]?.field,
            valueType: item.fields[0]?.valueType,
            isGroupBy: item.fields[0]?.isGroupBy,
            isSortBy: item.fields[0]?.isSortBy,
            order: item.fields[0]?.order,
          }
      );
      fetchAutomatedTable(
        content?.data[0]?.label.fields[0]?.table,
        fields,
        (content?.countOfRow ?? 'All').toString()
      ).then((res) => {
        const data = JSON.parse(res.data.getAutomaticTable.data) || [];
        setAutomaticTableData(
          data.map((item: any) => {
            return {
              value: item[content.data[0].value.fields[0].field],
              label: item[content.data[0].label.fields[0].field],
            };
          })
        );
      });
    }
  }, [content.isDynamic]);

  return (
    <>
      {content?.title && (
        <div className="widget-title" style={{ color }}>
          {content?.title}
        </div>
      )}

      {content?.subtitle && <div className="widget-sub-title">{content?.subtitle}</div>}
      <div className="blocks-wrapper">
        {!content.isDynamic &&
          content.data.map((block, index) => (
            <div
              key={index}
              className="block"
              style={{ backgroundColor: colors[index], color: index > 4 ? '#6e6e6e' : 'white' }}
            >
              <div className="block-label">{(+block.value)?.toFixed(2).replace('.', ',')} %</div>
              <div className="block-value">{block.label}</div>
            </div>
          ))}
        {content.isDynamic &&
          automaticTableData.map((block, index) => (
            <div
              key={index}
              className="block"
              style={{ backgroundColor: (content.colors || colors)[index], color: index > 4 ? '#6e6e6e' : 'white' }}
            >
              <div className="block-label">{block.value} %</div>
              <div className="block-value">{block.label}</div>
            </div>
          ))}
      </div>
    </>
  );
};
