// Export document panel
export const DocumentPanel: FC<{ apiKey: string }> = ({ apiKey }) => {
  const [docs, setDocs] = useState([]);

  const fetchData = async () =>
    client.query({
      query: GET_DOCUMENTS,
      variables: { customerId: apiKey },
    });

  useEffect(() => {
    fetchData().then((res) => {
      setDocs(res.data.getDocuments.documents);
    });
  }, []);

  return (
    <Stack spacing={30}>
      <List className='pdf_table'>
        {docs.map(({ name, url }, index) => (
          <ListItem className='pdf_table_row' key={index}>
            <ListItemText className='title'>
              <Typography>{name}</Typography>
            </ListItemText>
            <ListItemText className='pdf'>
              <a href={url} target="_blank">
                <img src="https://mainfirst.tools.factsheetslive.com/bundles/mainfirsttool/images/lc_pdf_icon_DE.png" />
              </a>
            </ListItemText>
          </ListItem>
        ))}
      </List>
      {/*<Table>*/}
      {/*  <TableBody>*/}
      {/*    {data2.map(({ a, b }, index) => (*/}
      {/*      <TableRow key={index}>*/}
      {/*        <TableCell>{a}</TableCell>*/}
      {/*        <TableCell>{b}</TableCell>*/}
      {/*      </TableRow>*/}
      {/*    ))}*/}
      {/*  </TableBody>*/}
      {/*</Table>*/}
    </Stack>
  );
};
