import { styled } from '@mui/material/styles';
import { Box as MuiBox, Tabs as MuiTabs } from '@mui/material';

export const Box = styled(MuiBox)({
  '.chart-loader': {
    position: 'absolute',
    width: '100%',
    height: '100%',
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    background: 'rgba(0,0,0,0.2)',
    zIndex: 100,
  },

  '.text-row': {
    fontSize: '20px',
    // fontFamily: 'Fira Sans Light',
    fontWeight: 300,
  },

  '.MenuItem_inner': {
    display:'inline-block',
    width: '100%',
    position: 'relative',
    paddingRight:'40px',
    svg: {
      position: 'absolute',
      right: 0,
      top: 0,
      bottom: 0,
      margin: 'auto 0',
    },
  },

  '.Tooltip': {
    position: 'relative',
    zIndex: 9999,

    '&:hover': {
      '.Tooltip-content': {
        opacity: 1,
        visibility: 'visible',
      }
    },

    '& .Tooltip-content': {
      position: 'absolute',
      left: '50%',
      transform: 'translateX(-50%)',
      width: '320px',
      fontSize: '14px',
      background: '#5d5d5d',
      padding: '8px',
      top: '100%',
      borderRadius: '5px',
      transition: 'all 0.3s ease',
      opacity: '0',
      visibility: 'hidden',
      color: '#fff',
    }
  },

  '@media (max-width:767px)': {
    '.Tooltip': {
      '.Tooltip-content': {
        left: 'auto',
        right: '-60px',
        transform: 'translateX(0)',
        width: '200px',
      }
    }
  },

  li: {
    '&.pdf_table_row': {
      display: 'flex',
      flexWrap: 'wrap',
      justifyContent: 'space-between',
      alignItems: 'center !important',
    },
    '&.MuiListItem-root': {
      alignItems: 'normal !important',
      '& .MuiListItemAvatar-root': {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
      }
    }
  },

  '.pdf_table_row': {
    '.title': {
      width: 'calc(100% - 40px)',
      '& + div': {
        width: '40px',
        a: {
          display: 'block',
          lineHeight: 0,
        }
      }
    }
  },
  '.MuiCollapse-vertical': {
    padding: '0 !important',
  },
  '.MuiList-root': {
    margin: '0 !important',
  },

  '.blocks-wrapper': {
    display: 'grid',
    gridTemplateColumns: 'repeat(5, 1fr)',
    gap: '4px',
    '@media (max-width: 678px)': {
      gridTemplateColumns: 'repeat(1, 1fr)',
    },
    p: {
      fontWeight: 900
    },
    '.block': {
      color: 'white',
      textAlign: 'center',
      height: '150px',
      padding: '0',
      '.block-label': {
        'fontSize': '24px',
        'fontWeight': 'bold',
        'marginBottom': '5px',
        'marginTop': '20px',
      },
      '.block-value': {
        'fontSize': '16px',
        'marginTop': 0,
        'padding': '0 10px'
      },
      '@media (max-width: 678px)': {
        width: '200px'
      },
    }
  },

  '.widget-title': {
    fontWeight: 600,
    fontSize: '24px',
    paddingTop: '16px',
    marginBottom: '16px',
  },

  '.widget-sub-title': {
    fontSize: '11px',
    marginBottom: '16px',
    color: '#6e6e6e',
  },

  '#apexchartsareaxdatetime': {
    '.apexcharts-yaxis-annotations': {
      '& > rect': {
        display: 'none',
      }
    },
    '.apexcharts-text.apexcharts-yaxis-annotation-label': {
      display: 'none',
    }
  },

  '.apexcharts-canvas:hover': {
    '.apexcharts-series-line': {
      path: {
        opacity: 0
      }
    },
    '.apexcharts-series-area': {
      path: {
        opacity: 0
      }
    }
  },

  '.custom-tooltip': {
    background: '#fefefe50',
    padding: '4px 8px',
    borderRadius: '0',
    border: '2px solid'
  },

  '.apexcharts-canvas': {
    '.apexcharts-series-bar': {
      rect: {
        '&:hover': {
          opacity: 1
        }
      }
    }
  },

  '.pie-chart-wrapper': {
    display: 'flex',
    alignItems: 'center',
    gap: '70px',
    marginBottom: '16px',
    '@media (max-width: 1200px)': {
      flexDirection: 'column',
      alignItems: 'start'
    },
    '.pie-chart-content': {
      width: '480px',
      display: 'flex',
      flexDirection: 'column',
      '.pie-chart-loading': {
        padding: '20px'
      },
      '@media (max-width: 678px)': {
        width: '100%',
      },
      '&.pie-chart-no-blocks': {
        '> div:first-child': {
          flexShrink: 0,
          width: '100%',
        },
      },
      '> div:first-child': {
        flexShrink: 0,
      },

      '.pie-chart-legend': {
        display: 'flex',
        flexDirection: 'column',
        gap: '8px',
        width: '200px',
        margin: 'auto',
        '.pie-chart-legend-item': {
          display: 'flex',
          justifyContent: 'space-between',
          alignItems: 'center',
          color: '#6e6e6e',
          fontWeight: 700,
          fontSize: '12px',
          '> div:first-child': {
            display: 'flex',
            alignItems: 'center',
            gap: '4px',
            '> div:first-child': {
              flexShrink: 0,
            }
          },
          '.pie-chart-legend-mark': {
            width: '16px',
            height: '16px'
          }
        },
      }
    },

    '.pie-chart-blocks-wrapper': {
      flexGrow: 1,
      display: 'flex',
      gap: '4px',
      '@media (max-width: 1200px)': {
        width: '100%',
        display: 'flex',
        flexWrap: 'wrap'
      },

      '.pie-chart-block': {
        color: 'white',
        textAlign: 'center',
        height: '125px',
        width: '125px',
        padding: '0',
        '@media (max-width: 1200px)': {
          minWidth: '200px',
        },
        '.block-label': {
          'fontSize': '24px',
          'fontWeight': 'bold',
          'marginBottom': '5px',
          'marginTop': '20px',
        },
        '.block-value': {
          'fontSize': '13px',
          'marginTop': 0,
          'padding': '0 10px',
        }
      }
    },

    '.legend-mouseover-inactive': {
      '&.apexcharts-datalabels': {
        opacity: '1 !important',
      }
    }
  },

  '@media (min-width:565px)': {
    '.pie-chart-wrapper': {
      '.pie-chart-content': {
        maxWidth: '480px',
        flexDirection: 'row',
        '.pie-chart-legend': {
          margin: 'inherit',
          marginTop: '16px',
        }
      },
    },
    
    '.pie-chart-loading': {
      padding: '20px'
    },
  },

  '@media (min-width:500px)': {
    '.pie-chart-wrapper' : {
      '.pie-chart-content': {
        '&.pie-chart-no-blocks': {
          '> div:first-child': {
            flexShrink: 0,
            width: '300px',
          },
        },
      },
    },
    
    
    '.pie-chart-loading': {
      padding: '20px'
    },
  },

  '.MuiListItemAvatar-root': {
    textAlign: 'center',
    span: {
      fontSize: '34px',
      textAlign: 'center',
    }
  },
  '& .MuiListItemText-root': {
    flex: 'none',
  },
  '.Tooltip_wrapper': {
    display: 'inline-flex',
    alignItems: 'center',
    lineHeight: '1.1',
    'svg': {
      marginLeft: '3px',
      width: '21px',
    }
  },
  '& ._MenuItem': {
    whiteSpace: 'break-spaces !important',
  },

  '& .current_positions_list': {
    'li': {
      '& > div': {
        // width: '25% !important',
      }
    }
  },
  '& .list_heading_style_0': {
    fontWeight: '600',
    backgroundColor: '#F2F2F2',
    span: {
      fontWeight: 500,
    }
  },
  '& .list_heading_style_1': {
    fontWeight: '600',
    borderBottom: '2px solid #9c9c9c',
    borderTop: '2px solid #9c9c9c',
    marginBottom: '1px',
    '& .MuiListItemText-root': {
      boxSizing: 'border-box',
      borderRight: '2px solid white',
      borderLeft: '2px solid white',
      color: '#9c9c9c',
      span: {
        fontWeight: 'bold',
      },
      '&:not(:first-child)': {
        textAlign: 'right',
        color: '#6e6e76'
      },
    },
    '@media (max-width: 767px)': {
      '&.width-full': {
        width: '100% !important',
      },
    },
    '@media (max-width: 678px)': {
      '&.width-full': {
        width: '100% !important',
      },
    }
  },
  '& .list_content_style_1': {
    '&.MuiListItem-root': {
      border: 'none',
      '& .MuiListItemText-root': {
        boxSizing: 'border-box',
        borderRight: '2px solid white',
        borderLeft: '2px solid white',
        color: '#6e6e76',
        background: '#bcbcbc',
        wordWrap: 'break-word',
        span: {
          fontWeight: 'bold',
        },
        '&:not(:first-child)': {
          background: '#f2f2f2',
          textAlign: 'right',
        },
      },
      '@media (max-width: 767px)': {
        '&.width-full': {
          width: '100% !important',
        },
      },
      '@media (max-width: 678px)': {
        '&.width-full': {
          width: '100% !important',
        },
      }
    }
  },
  '& .list_heading_style_2': {
    fontWeight: '600',
    borderBottom: '2px solid #d0d0d0',
    borderTop: '2px solid #9d9d9c',
    marginBottom: '1px',
    '& .MuiListItemText-root': {
      color: '#9c9c9c',
      span: {
        fontWeight: 'bold',
      },
    }
  },
  '& .list_content_style_2': {
    '&.MuiListItem-root': {
      borderBottom: '2px solid #d0d0d0',
      '&:first-child': {
        borderTop: '2px solid #9d9d9c',
      },
      '& .MuiListItemText-root': {
        color: '#6e6e76',
        textAlign: 'right',
        '&:first-child': {
          textAlign: 'left',
          span: {
            fontWeight: 'bold',
          },
        },
        '@media (max-width: 767px)': {
          // width: '350px !important',
          wordWrap: 'break-word',
        },
        '@media (max-width: 678px)': {
          // width: '250px !important',
          wordWrap: 'break-word',
        }
      },
      '@media (max-width: 767px)': {
        '&.width-full': {
          width: '100% !important',
        },
      },
      '@media (max-width: 678px)': {
        '&.width-full': {
          width: '100% !important',
        },
      }
    },
  },
  '.MuiListItemText-root': {
    "a": {
      "&.list-link-text": {
        color: 'inherit',
        textDecoration: 'none',
        '&:hover': {
          color: '#467096',
          textDecoration: 'underline',
        }
      }
    }
  },
  '& .controversial_business_areas': {
    '& > li': {
      '& > div': {
        '&:not(:first-child)': {
          width: '20%',
        },
        '&:not(:nth-child(2))': {
          'textAlign': 'left',
        }
      }
    }
  },

  'ul': {
    '& .scroll-mobile': {
      margin: 0
    }
  },
  '@media (max-width: 767px)': {
    ul: {
      '&.MuiList-root': {
        '&.scroll-mobile': {
          overflow: 'auto',
          '& > li': {
            width: '760px',
          },
          '&.single-col': {
            '& > li': {
              width: '100%',
            }
          }
        }
      }
    },
    '.MuiListItemText-root': {
      '&.title': {
        width: '350px',
      }
    },
    '._Tabs': {
      '& .MuiTabs-flexContainer': {
        button: {
          padding: '11px 10px !important',
        },
        a: {
          padding: '11px 10px !important',
        }
      }
    }
  },

  '& .apexcharts-zoom-icon.apexcharts-selected': {
    svg: {
      fill: '#6E8192 !important',
    }
  }
});

export const Tabs = styled(MuiTabs)({
  background: '#ccd5df',
  display: 'flex',
  alignItems: 'center',
  borderBottom: '0 !important',

  '&.theme_01': {
    '&.theme_04': {
      background: '#D3BEAF',
      '& .MuiTabs-flexContainer': {
        button: {
          '&:hover': {
            color: '#945C33',
          },
          '&.Mui-selected': {
            color: '#945C33',
            borderBottomColor: '#945C33'
          }
        },
      },
    },
    '&.theme_05': {
      background: '#f6dfc4',
      '& .MuiTabs-flexContainer': {
        button: {
          '&:hover': {
            color: '#cd7309',
          },
          '&.Mui-selected': {
            color: '#cd7309',
            borderBottomColor: '#cd7309'
          }
        },
      },
    },
    '& .MuiTabs-flexContainer': {
      justifyContent: 'space-around',

      button: {
        fontSize: '16px',
        background: 'transparent',
        padding: '0 10px 2px',
        minHeight: 'auto',
        borderBottom: '2px solid transparent',
        '&:hover': {
          color: '#002d5c',
        },
        '&.Mui-selected': {
          color: '#002d5c',
          borderBottomColor: '#002d5c'
        }
      },
      a: {
        background: 'transparent',
        padding: '0 10px 2px',
        minHeight: 'auto',
        borderBottom: '2px solid transparent',
        '&:hover': {
          color: '#002d5c',
        },
        '&.Mui-selected': {
          color: '#002d5c',
          borderBottomColor: '#002d5c'
        }
      },
    },
  },

  '&.theme_02': {
    flexDirection: 'column',
    backgroundColor: 'transparent',
    overflow: 'visible !important',

    '&.theme_03' : {
      '& .MuiTabs-flexContainer': {
        button: {
          '&.Mui-selected': {
            backgroundColor: '#467097',
            borderColor: '#467097',
            '&:after': {
              borderTop: '10px solid #467097',
            }
          }
        }
      },

      '.apexcharts-legend-marker': {
        borderRadius: 0,
      }
    },

    '&.theme_04' : {
      '& .MuiTabs-flexContainer': {
        button: {
          '&.Mui-selected': {
            backgroundColor: '#894e23',
            borderColor: '#894e23',
            '&:after': {
              borderTop: '10px solid #894e23',
            }
          }
        }
      }
    },

    '&.theme_05' : {
      '& .MuiTabs-flexContainer': {
        button: {
          '&.Mui-selected': {
            backgroundColor: '#cd7309',
            borderColor: '#cd7309',
            '&:after': {
              borderTop: '10px solid #cd7309',
            }
          }
        }
      }
    },

    '& .MuiTabs-scroller': {
      display: 'flex',
      flexDirection: 'column',
      overflow: 'visible !important',
    },

    '& .MuiTabs-flexContainer': {
      flex: 1,
      display: 'flex',
      alignItems: 'center',
      overflow: 'visible !important',

      button: {
        flex: 1,
        height: '52px',
        fontSize: '18px',
        border: '1px solid #0d1c3e',
        margin: 0,
        backgroundColor: 'transparent',
        color: '#0d1c3e',
        '&:hover': {
          backgroundColor: 'transparent',
          color: '#0d1c3e',
        },
        '&.Mui-selected': {
          overflow: 'visible',
          position: 'relative',
          color: 'white',
          backgroundColor: '#D0BB8C',
          borderColor: '#D0BB8C',
          '&:after': {
            borderBottom: 0,
            borderLeft: '10px solid transparent',
            borderRight: '10px solid transparent',
            borderTop: '10px solid #D0BB8C',
            bottom: '-10px',
            content: '""',
            height: 0,
            left: 0,
            margin: '0 auto',
            position: 'absolute',
            right: 0,
            width: 0,
            zIndex: 1,
          }
        }
      }
    }
  },
});
