import { FC } from 'react';
import { PieChart } from './pie-chart';
import { Collapse } from './collapse';
import { TextRow } from './textRow';
import { BarChart } from './bar-chart';
import { SolActivePanel } from '../panels/SolActive';
import { DocumentList } from './document-list';
import { Table } from './table';
import { RichText } from './rich-text';
import { Blocks } from './blocks';
import { ITheme } from '.';
import { RiskIndicator } from './risk-indicator';

interface SpacerProps {
  content: number | string;
}

export const Spacer: FC<SpacerProps> = ({ content }) => {
  const size = typeof content === 'number' ? content : Number(content);
  const height = Number.isFinite(size) ? size : 0;
  return (
    <div style={{ height }} aria-hidden="true" />
  );
}

export type WidgetProps = {
  type: string;
  content: WidgetProps[] | any;
  isin: string;
  color: string;
  theme?: ITheme;
};

export const WidgetBuilder: FC<WidgetProps> = ({ content, type, isin, color, theme }) => {
  const buildWidget = () => {
    switch (type) {
      case 'container': {
        return (
          <>
            {content.map((widget: WidgetProps) => (
              <>
                <WidgetBuilder {...widget} isin={isin} color={color} theme={theme} />
              </>
            ))}
          </>
        )
      }

      case 'collapse': {
        return (
          <>
            <Collapse content={content} color={color} theme={theme} />
          </>
        )
      }

      case 'spacer': {
        return (
          <Spacer content={content} />
        )
      }

      case 'pie-chart': {
        return (
          <PieChart content={content} color={color} theme={theme} />
        )
      }

      case 'bar-chart': {
        return (
          <BarChart content={content} color={color} theme={theme} />
        )
      }

      case 'risk-indicator': {
        return (
          <RiskIndicator content={content} color={color} theme={theme} />
        )
      }

      case 'text-row': {
        return (
          <TextRow content={content} color={color} isin={isin} />
        )
      }

      case 'history': {
        return (
          <SolActivePanel content={content} isin={isin} color={color} theme={theme} />
        )
      }

      case 'document-list': {
        return (
          <DocumentList content={content} color={color} />
        )
      }

      case 'table': {
        return (
          <Table theme={theme} content={content} color={color} isin={isin} />
        )
      }

      case 'rich-text': {
        return (
          <>
            <RichText content={content} color={color} theme={theme} />
          </>
        )
      }

      case 'block': {
        return (
          <Blocks content={content} color={color} theme={theme} />
        )
      }

      default:
        return (<></>)
    }
  };

  return (
    <>
      {buildWidget()}
    </>
  )
}
