import React, {FC, useState} from 'react';
import {Collapse as MuiCollapse, MenuItem} from '@mui/material';
import KeyboardArrowUpIcon from '@mui/icons-material/KeyboardArrowUp';
import KeyboardArrowDownIcon from '@mui/icons-material/KeyboardArrowDown';
import {WidgetBuilder, WidgetProps} from './widget-builder';
import { ITheme } from '.';

export type Props = {
  color: string;
  content: WidgetProps[] | any;
  theme?: ITheme;
};

export const Collapse: FC<Props> = ({ content, color, theme }) => {
  const [expanded, setExpanded] = useState(false);
  const handleToggleCollapse = () => {
    setExpanded(!expanded);
  };
  return (
    <>
      <MenuItem
        className='_MenuItem'
        onClick={handleToggleCollapse}
        sx={{
          ...(theme ? {
            color: theme?.colorSet.primary
          } : {})
        }}
      >
        <div className='MenuItem_inner'>
          {content.title}
          {expanded ? <KeyboardArrowUpIcon/> : <KeyboardArrowDownIcon/>}
        </div>
      </MenuItem>
      <MuiCollapse in={expanded}>
        {content?.widgets?.map((widget: WidgetProps) => (
          <>
            <WidgetBuilder {...widget} color={color} theme={theme} />
          </>
        ))}
      </MuiCollapse>
    </>
  )
}
