import { FC } from 'react';
import ReactApexChart from 'react-apexcharts';
import { ApexOptions } from 'apexcharts';
import {IChartItem} from './index';
import {CircularProgress} from '@mui/material';

export const BranchenChart : FC<{ data: IChartItem[] | undefined, loading?: boolean }>  = ({ data, loading }) =>{
    const chartData  = {
        series: data?.map(item => item.value) || [],
        options: {
          labels: data?.map(item => item.label) || [],
          colors:['#0461B8','#165B9D','#275681','#395066','#4A4A4A'],
          chart: {
            width: 400,
            type: 'donut',
          },
          legend: {
            show: true,
            position: "right",
            containerMargin: {
              top: 0
            },
            fontSize: '16px',
            // fontFamily: 'Fira Sans',
            fontFamily: 'inherit'
          },
          responsive: [
            {
              breakpoint: 600,
              options: {
                legend: {
                  fontSize: "11px",
                  position: "bottom",
                  width: "100%",
                }
              }
            }
          ]
        },
    }

    return(
        <div className="pie-chart-content">
            {loading ? (
              <div className="pie-chart-loading">
                <CircularProgress />
              </div>
            ) : (
              <ReactApexChart options={chartData.options as ApexOptions} series={chartData.series} type="donut" />
            )}
        </div>
    )
}
