import React, { FC } from 'react';
import { Box, Divider } from '@mui/material';
import { ITheme } from './../widgetBuilder';

type Props = {
  color: string;
  content: any;
  theme?: ITheme;
}

export const RichText: FC<Props> = ({ content, theme: widgetTheme }) => {
  return (
    <>
      <Box sx={{ padding: '10px 0', ...(widgetTheme?.colorSet.tableText ? { color: widgetTheme?.colorSet.tableText, a: { color: `${widgetTheme?.colorSet.secondary} !important` } } : {}) }}>
        <div dangerouslySetInnerHTML={{ __html: content }} />
      </Box>
      <Divider />
    </>
  )
};