import {FC, useMemo} from 'react';
import ReactApexChart from 'react-apexcharts';
import {ApexOptions} from 'apexcharts';
import {COLOR_SETS} from '../../../contants/colors';
import { ITheme } from '.';
import { generateGradientColors } from '../../../utils/generateColorSets';

type Props = {
  color: string;
  content: {
    title?: string;
    subtitle?: string;
    showBlocks: boolean;
    chartData: {
      value: number;
      label: string;
    }[];
  },
  theme?: ITheme;
}

export const PieChart : FC<Props>  = ({ color, content, theme }) =>{
  const colors = theme ? (theme?.colorSet.gradientColors || generateGradientColors(theme?.mainColor, 8)) : COLOR_SETS[color.toLowerCase()] || COLOR_SETS['#ccd5df'];
  const isFirstFond = color === '#ccd5df';

  const chartData  = useMemo(() => (
    {
      series: content?.chartData?.map((item) => +item.value) || [],
      options: {
        labels: content?.chartData?.map((item) => item.label) || [],
        stroke: {
          width: 0,
        },
        plotOptions: {
          pie: {
            donut: {
              customScale: 0.8,
            },
          },
        },
        dataLabels: {
          enabled: isFirstFond, // Set this to false to hide the labels
        },
        colors,
        chart: {
          type: 'donut',
        },
        markers: {
          shape: 'square',
          size: 50
        },
        legend: {
          show: isFirstFond,
          width: 200,
          position: "right",
          containerMargin: {
            top: 0
          },
          fontSize: '16px',
          fontFamily: 'inherit'
          // fontFamily: 'Fira Sans',
        },
        responsive: [
          {
            breakpoint: 600,
            options: {
              legend: {
                fontSize: "11px",
                position: "bottom",
                width: "100%",
              }
            }
          }
        ]
      },
    }
  ), [content]);

  return(
    <>
      {content?.title && (
        <div className="widget-title" style={{ color }}>
          {content?.title}
        </div>
      )}

      {content?.subtitle && (
        <div className="widget-sub-title">
          {content?.subtitle}
        </div>
      )}

      <div className="pie-chart-wrapper">

        <div className={`pie-chart-content ${!content.showBlocks ? 'pie-chart-no-blocks' : ''}`}>
          <ReactApexChart options={chartData.options as ApexOptions} series={chartData.series} type="donut" />

          {!isFirstFond && (
            <div className="pie-chart-legend">
              {content?.chartData?.map((item, index) => (
                <div className="pie-chart-legend-item">
                  <div>
                    <div className="pie-chart-legend-mark" style={{ backgroundColor: colors[index] }} />
                    {item.label}
                  </div>
                  <div>
                    {item.value}%
                  </div>
                </div>
              ))}
            </div>
          )}
        </div>
        {
          content.showBlocks && (
            <div className="pie-chart-blocks-wrapper">
              {content?.chartData.map((block, index) => (
                <div key={index} className="pie-chart-block" style={{ backgroundColor: colors[index] }}>
                  <div className="block-label">{(+block.value)?.toFixed(2).replace('.', ',')} %</div>
                  <div className="block-value">{block.label}</div>
                </div>
              ))}
            </div>
          )
        }
      </div>
    </>
  )
}
