import React, {FC, useEffect, useState} from 'react';
import { WidgetProps } from './widget-builder';
import {Divider, Typography} from '@mui/material';
import {client} from '../../../App';
import {GET_AUTOMATED_COLUMNS} from '../../../queries/widgets';

export type Props = {
  color: string;
  content: WidgetProps[] | any;
  isin: string;
};

export const TextRow: FC<Props> = ({ content, color, isin }) => {
  const fetchAutomatedColumns = async (columns: any[]) =>
    client.query({
      query: GET_AUTOMATED_COLUMNS,
      variables: { param: { data: columns, isin: isin } }
    });

  const [dynamicText, setDynamicText] = useState("");

  useEffect(() => {
    if (content?.isDynamic) {
      fetchAutomatedColumns([content]).then((res) => {
        setDynamicText(res.data?.getAutomatedColumns?.[0]?.[0]);
      });
    }
  }, [content]);

  return (
    <>
      <Typography className="text-row">
        {content?.isDynamic ? dynamicText : content}
      </Typography>
    </>
  )
}
