// Dependencies
import { FC } from 'react';

// Pages
import { FactSheetPage, PerformancePage, ValuePage } from '../pages';
import { CdnWidgets } from '../pages/FactSheet/widgetBuilder';

// Interfaces
interface IRoute {
  page: PageType;
  component: FC<any>;
}

// Page
export type PageType = 'performance' | 'value' | 'factsheet' | 'test'

// Export main routes
export const MAIN_ROUTES = (widgetId?: string): IRoute[] =>( [
  {
    page: 'performance',
    component: PerformancePage,
  },
  {
    page: 'value',
    component: ValuePage,
  },
  {
    page: 'factsheet',
    component: FactSheetPage,
  },
  ...(widgetId ? [{
    page: 'test' as PageType,
    component: CdnWidgets,
  }] : [])
]);
