export const generateColorScheme = (mainColor: string) => {

  // Convert to HSL if input is hex
  if (mainColor.startsWith('#')) {
    mainColor = hexToHsl(mainColor);
  }

  // Extract HSL values
  // eslint-disable-next-line @typescript-eslint/ban-ts-comment
  // @ts-ignore
  const [hue, saturation, lightness] = mainColor.match(/\d+/g).map(Number);

  // Define color scheme
  const colorScheme = [];
  colorScheme.push(mainColor); // Main color

  const lightnessIncrease = (100 - lightness) * 0.5; // 50% increase in lightness

  colorScheme.push(`hsl(${hue}deg ${saturation*0.8}% ${lightness + lightnessIncrease*0.3}%)`);
  colorScheme.push(`hsl(${hue}deg ${saturation*0.9}% ${lightness + lightnessIncrease*0.5}%)`);

  colorScheme.push(`hsl(${hue}deg ${saturation}% ${lightness + lightnessIncrease*0.7}%)`);
  colorScheme.push(`hsl(${hue}deg ${saturation}% ${lightness + lightnessIncrease*0.9}%)`);
  colorScheme.push(`hsl(${hue}deg ${saturation}% ${lightness + lightnessIncrease}%)`);

  return colorScheme;
}

export const hexToHsl = (H: string): string => {
  // Convert hex to RGB first
  let r: any = 0;
  let g: any = 0;
  let b: any = 0;
  if (H.length == 4) {
    r = "0x" + H[1] + H[1];
    g = "0x" + H[2] + H[2];
    b = "0x" + H[3] + H[3];
  } else if (H.length == 7) {
    r = "0x" + H[1] + H[2];
    g = "0x" + H[3] + H[4];
    b = "0x" + H[5] + H[6];
  }
  // Then to HSL
  r /= 255;
  g /= 255;
  b /= 255;
  const cmin = Math.min(r,g,b);
  const cmax = Math.max(r,g,b);
  const delta = cmax - cmin;
  let h = 0;
  let s = 0;
  let l = 0;

  if (delta == 0)
    h = 0;
  else if (cmax === r)
    h = ((g - b) / delta) % 6;
  else if (cmax === g)
    h = (b - r) / delta + 2;
  else
    h = (r - g) / delta + 4;

  h = Math.round(h * 60);

  if (h < 0)
    h += 360;

  l = (cmax + cmin) / 2;
  s = delta == 0 ? 0 : delta / (1 - Math.abs(2 * l - 1));
  s = +(s * 100).toFixed(1);
  l = +(l * 100).toFixed(1);

  return "hsl(" + h + "," + s + "%," + l + "%)";
}

const hexToRgb = (hex: string) => {
  const result = /^#?([a-f\d]{2})([a-f\d]{2})([a-f\d]{2})$/i.exec(hex);
  return result ? {
    r: parseInt(result[1], 16),
    g: parseInt(result[2], 16),
    b: parseInt(result[3], 16)
  } : null;
};

const rgbToHex = (r: number, g: number, b: number): string => {
  return "#" + ((1 << 24) + (r << 16) + (g << 8) + b).toString(16).slice(1);
};

export const generateGradientColors = (mainColor: string, count: number): string[] => {
  const colors: string[] = [];
  const mainRgb = hexToRgb(mainColor);
  
  if (!mainRgb) return colors;
  
  for (let i = 0; i < count; i++) {
    const factor = (i + 1) / count;
    const newRgb = {
      r: Math.round(mainRgb.r + (255 - mainRgb.r) * factor * 0.8),
      g: Math.round(mainRgb.g + (255 - mainRgb.g) * factor * 0.8),
      b: Math.round(mainRgb.b + (255 - mainRgb.b) * factor * 0.8),
    };
    colors.push(rgbToHex(newRgb.r, newRgb.g, newRgb.b));
  }
  
  return colors;
};