// Dependencies
import React, { FC, useState } from 'react';
import { Tab, useMediaQuery } from '@mui/material';
import { useParams } from 'react-router-dom';

// Panels
import { DocumentPanel, ESGPanel, PortfolioPanel, PortraitPanel } from './panels';

// Theme
import theme from '../../theme';
import { SolActivePanel } from './panels/SolActive';
import { Box, Tabs } from './styles';

// Interfaces
interface ITab {
  label: string;
  id: string | number;
  panel?: FC<any>;
}

// Tabs
const navTabs: ITab[] = [
  {
    label: 'TRANCHE',
    id: 'LU2439874319',
  },
  {
    label: 'A',
    id: 'LU2439874319',
  },
  {
    label: 'B',
    id: 'LU2439874319',
  },
  {
    label: 'C',
    id: 'LU2439874319',
  },
  {
    label: 'D',
    id: 'LU2439874319',
  },
  {
    label: 'R',
    id: 'LU2439874319',
  },
  {
    label: 'X',
    id: 'LU2439874319',
  },
];

const tabs: ITab[] = [
  {
    label: 'PORTRAIT',
    id: 1,
    panel: PortraitPanel,
  },
  {
    label: 'WERTENTWICKLUNG',
    id: 2,
    panel: SolActivePanel,
  },
  {
    label: 'PORTFOLIO',
    id: 3,
    panel: PortfolioPanel,
  },
  {
    label: 'ESG',
    id: 4,
    panel: ESGPanel,
  },
  {
    label: 'DOKUMENTE',
    id: 5,
    panel: DocumentPanel,
  },
];

// Export fact-sheet age
export const FactSheetPage: FC<{ apiKey: string }> = ({ apiKey }) => {
  // States
  const [activeTabId, setActiveTabId] = useState<number | string>(tabs[0].id);

  // Check window size
  const isXs = useMediaQuery(theme.breakpoints.only('xs'));
  const isSm = useMediaQuery(theme.breakpoints.down('md'));

  // Get active nav id from hook
  const { id: activeNavId } = useParams<{ id: string }>();

  // Tab change handler
  const handleTabChange = (_: React.SyntheticEvent, activeId: number) => {
    setActiveTabId(activeId);
  };

  // Export fact-sheet page
  return (
    <Box>
      <Tabs className='_Tabs theme_01' value={activeTabId} orientation={isSm ? 'vertical' : 'horizontal'} onChange={handleTabChange}>
        {tabs.map(({ label, id }, index) => (
          <Tab
            sx={{ fontWeight: 400 }}
            key={index}
            value={id}
            label={label}
          />
        ))}
      </Tabs>
      {tabs.map(({ id, panel: P }: any) => (
        <React.Fragment key={id}>{activeTabId === id && P && <P apiKey={apiKey} isin="LU2439874319" />}</React.Fragment>
      ))}
    </Box>
  );
};
