// Dependencies
import React, { FC, useEffect } from 'react';
import { onError } from '@apollo/client/link/error';
import { ApolloClient, ApolloProvider, from, HttpLink, InMemoryCache } from '@apollo/client';
import { ThemeProvider } from '@mui/material';

// Theme
import theme from './theme';
import { GlobalStyle } from './styles';

// Constants
import { MAIN_ROUTES, PageType, SERVER_API } from './contants';
import { typeDefs } from './queries/type';

// Prepare links
const errorLink = onError((errors: any) => {
  if (errors.graphqlErrors) {
    errors.graphqlErrors.map((data: any) => {
      alert(`Graphql error ${data.message}`);
    });
  }
});

const apiKey = localStorage.getItem('apiKey');

const link = from([errorLink, new HttpLink({ uri: SERVER_API, headers: { apiKey } })]);

// Create client
export const client = new ApolloClient({
  cache: new InMemoryCache(),
  link: link,
  typeDefs
});

interface IAppProps {
  customerId: string
  widgetId: string
  page: PageType
}

// Create app
const App: FC<IAppProps> = ({ customerId, widgetId, page }) => {
  const Component: FC<{ apiKey: string} > | undefined = MAIN_ROUTES(widgetId).find((route) => route.page === (widgetId ? "test" : page))?.component;

  useEffect(() => {
    localStorage.setItem('customerId', customerId);
  }, [apiKey]);

  return (
    <>
      <GlobalStyle />
      <ApolloProvider client={client}>
        <ThemeProvider theme={theme}>
          <GlobalStyle />
          {
            Component && <Component apiKey={widgetId || customerId} />
          }
        </ThemeProvider>
      </ApolloProvider>
    </>
  )
};

// Export app
export default App;
