import { gql } from '@apollo/client';

export const GET_ESG_AREAS = gql`
  query getEsgAreas($customerId: String!) {
    getEsgAreas(customerId: $customerId) {
      data {
        _id
        logo_url
        exclusionCriteria
        noSales
        sales_value_less_5
        sales_value_more_5
      }
      esgAreaDate
    }
  }
`;

export const GET_ESG_DATA = gql`
  query getEsg($customerId: String!) {
    getEsg(customerId: $customerId) {
      data {
        _id
        practices
        practiceDate
      }
    }
  }
`;

export const GET_ESG_DOCUMENTS = gql`
  query getEsgDocuments($customerId: String!) {
    getEsgDocuments(customerId: $customerId) {
      documents {
        _id
        name
        url
        is_editable
      }
    }
  }
`;
