import {List, ListItem, ListItemText} from '@mui/material';
import React from 'react';

type Props = {
  color: string;
  content: { label: string; url: string }[];
}

export const DocumentList = ({ color, content }: Props) => {
  return (
    <List>
      {content.map(({ label, url }, index) => (
        <ListItem key={index}>
          <ListItemText>{label}</ListItemText>
          <ListItemText sx={{ textAlign: 'right', marginLeft: 'auto' }}>
            <a href={url} target="_blank">
              <img src="https://mainfirst.tools.factsheetslive.com/bundles/mainfirsttool/images/lc_pdf_icon_DE.png" alt="document" />
            </a>
          </ListItemText>
        </ListItem>
      ))}
    </List>
  )
};
