import { FC, useMemo } from 'react';
import { Box } from '@mui/material';
import { ITheme } from '.';

interface RiskIndicatorContent {
  riskCount: number; // Selected risk level (1-based)
  colors: string[]; // [startColor, endColor]
  labelLow: string;
  labelHigh: string;
  descriptionLow: string;
  descriptionHigh: string;
  totalLevels?: number; // Optional, defaults to 7
}

interface Props {
  content: RiskIndicatorContent;
  color: string;
  theme?: ITheme;
}

export const RiskIndicator: FC<Props> = ({ content, theme }) => {
  const totalLevels = content?.totalLevels && content.totalLevels > 0 ? content.totalLevels : 7;
  const activeIndex = Math.min(Math.max(1, content?.riskCount || 1), totalLevels);
  const [startColor, endColor] = [content?.colors?.[0] || '#9EE493', content?.colors?.[1] || '#6EA8FE'];

  const boxBg = '#eef1f5';
  const boxText = theme?.colorSet?.tableText || '#4c566a';
  const activeBg = theme?.colorSet?.primary || endColor;
  const activeText = '#fff';
  const arrowHeight = 24;
  const arrowHead = 18;

  const numbers = useMemo(() => Array.from({ length: totalLevels }, (_, i) => i + 1), [totalLevels]);

  return (
    <Box sx={{ p: 16 }}>
      {/* Top scale blocks */}
      <Box
        sx={{
          display: 'grid',
          gridTemplateColumns: `repeat(${totalLevels}, minmax(0, 1fr))`,
          gap: '8px',
          mb: 1.5,
        }}
      >
        {numbers.map((n) => {
          const isActive = n === activeIndex;
          return (
            <Box
              key={n}
              sx={{
                textAlign: 'center',
                borderRadius: '10px',
                background: isActive ? activeBg : boxBg,
                color: isActive ? activeText : boxText,
                padding: '10px 0',
                fontWeight: 700,
                fontSize: '14px',
                border: isActive ? '1px solid transparent' : '1px solid #d8dee9',
              }}
            >
              {n}
            </Box>
          );
        })}
      </Box>

      {/* Upper labels */}
      <Box
        sx={{
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'space-between',
          color: theme?.colorSet?.tableText || '#4c566a',
          fontWeight: 600,
          mt: 8,
          mb: 16,
          px: 0.5,
          fontSize: { xs: '12px', sm: '13px' },
        }}
      >
        <span>{content?.labelLow}</span>
        <span>{content?.labelHigh}</span>
      </Box>

      {/* Gradient arrow with two heads */}
      <Box sx={{ position: 'relative', width: '100%', height: `${arrowHeight}px`, mb: 1.5 }}>
        <Box
          sx={{
            position: 'absolute',
            left: `${arrowHead}px`,
            right: `${arrowHead}px`,
            top: 0,
            bottom: 0,
            borderRadius: 0,
            background: `linear-gradient(90deg, ${startColor} 0%, ${endColor} 100%)`,
          }}
        />
        {/* Left arrow head */}
        <Box
          sx={{
            position: 'absolute',
            left: 0,
            top: 0,
            width: 0,
            height: 0,
            borderTop: `${arrowHeight / 2}px solid transparent`,
            borderBottom: `${arrowHeight / 2}px solid transparent`,
            borderRight: `${arrowHead}px solid ${startColor}`,
          }}
        />
        {/* Right arrow head */}
        <Box
          sx={{
            position: 'absolute',
            right: 0,
            top: 0,
            width: 0,
            height: 0,
            borderTop: `${arrowHeight / 2}px solid transparent`,
            borderBottom: `${arrowHeight / 2}px solid transparent`,
            borderLeft: `${arrowHead}px solid ${endColor}`,
          }}
        />
      </Box>

      {/* Lower descriptions */}
      <Box
        sx={{
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'space-between',
          color: theme?.colorSet?.tableText || '#4c566a',
          fontWeight: 500,
          mt: 8,
          px: 0.5,
          fontSize: { xs: '12px', sm: '13px' },
        }}
      >
        <span>{content?.descriptionLow}</span>
        <span>{content?.descriptionHigh}</span>
      </Box>
    </Box>
  );
};


