// Dependencies
import React from 'react';
import ReactDOM from 'react-dom/client';

// App
import App from './App';

// Styles
import { PageType } from './contants';

export const renderCdnRoots = () => {
  const cdnElements = document.getElementsByClassName('cdn-root');

  for (let i = 0; i < cdnElements.length; i ++) {
    const element = cdnElements[i];
    const root = ReactDOM.createRoot(element);

    const customerId = element?.getAttribute('customerId') || '';
    const widgetId = element?.getAttribute('widgetId') || '';
    const page = element?.getAttribute('page') || '';

    root.render(
      <>
        <App customerId={customerId} widgetId={widgetId} page={page as PageType} />
      </>
    )
  }
}

renderCdnRoots();

const observer = new MutationObserver((mutationsList) => {
  for (const mutation of mutationsList) {
    if (mutation.type === 'childList') {
      // eslint-disable-next-line @typescript-eslint/ban-ts-comment
      // @ts-ignore
      for (const node of mutation.addedNodes) {
        if (node.nodeType === Node.ELEMENT_NODE && node.classList.contains('cdn-root')) {
          const customerId = node.getAttribute('customerId') || '';
          const widgetId = node.getAttribute('widgetId') || '';
          const page = node.getAttribute('page') || '';
          // If a new cdn-root element is added, render the components for it
          const root = ReactDOM.createRoot(node);
          root.render(
            <>
              <App customerId={customerId} widgetId={widgetId} page={page as PageType} />
            </>
          );
        }
      }
    }
  }
});

observer.observe(document.body, { childList: true, subtree: true });
