import { gql } from '@apollo/client';

export const GET_PORTFOLIO = gql`
  query getPortfolio($customerId: String!) {
    getPortfolio(customerId: $customerId) {
      _id
      region {
        label
        value
      }
      regionDate
      branchen {
        label
        value
      }
      branchenDate
      market {
        label
        value
      }
      marketDate
    }
  }
`;

export const GET_CIXON_PORTFOLIO = gql`
  query getCixonPortfolio {
    getCixonPortfolio {
      _id
      isin
      emittent
      anteliamfv
      datum
    }
  }
`;
