import { List, ListItem, ListItemText, ListItemAvatar, Dialog, DialogTitle, DialogContent, DialogActions, Button, Typography, IconButton } from '@mui/material';
import CloseIcon from '@mui/icons-material/Close';
import { useMediaQuery } from '@mui/material';
import React, { useEffect, useState } from 'react';
import InfoSharpIcon from '@mui/icons-material/InfoSharp';
import moment from 'moment';
import { client } from '../../../App';
import { GET_QUERY } from '../../../queries/history';
import { GET_AUTOMATED_COLUMNS, GET_AUTOMATED_TABLE, GET_CELL, GET_TABLE } from '../../../queries/widgets';
import { styled } from '@mui/material/styles';
import { ITheme } from './../widgetBuilder';

const CustomStyledLink = styled('a')(({ color }) => ({
  '&:hover': {
    color: `${color} !important`,
  },
  '@media (max-width:600px)': {
    fontSize: '14px',
  },
}));

type Content = {
  title?: any;
  type: string;
  isin: string;
  themeIndex: number;
  body: any[][];
  header: string[];
  showHeader: boolean;
  countOfRow?: string | number;
  columns: {
    type: string;
    showColumnTooltip: boolean;
    showValueConfiguration: boolean;
    columnTooltip: string;
  }[];
  rowSetting?: {
    rowPos: number;
    fontWeight: string;
    fontColor: string;
    backgroundColor: string;
  }[]
};

type Props = {
  isin: string;
  color: string;
  content: Content;
  theme?: ITheme;
};

export const Table = ({ color, content: originalContent, isin, theme: widgetTheme }: Props) => {
  const isMobile = useMediaQuery('(max-width:600px)');
  const [content, setContent] = useState<Content>();
  const [tableData, setTableData] = useState<any>({});
  const [dynamicTitle, setDynamicTitle] = useState('');
  const [confirmDialogOpen, setConfirmDialogOpen] = useState(false);
  const [pendingLinkUrl, setPendingLinkUrl] = useState<string | null>(null);
  const [pendingRowConfirm, setPendingRowConfirm] = useState<any | null>(null);
  const [isCancelAlert, setIsCancelAlert] = useState(false);
  const fetchData = async (mode: string) =>
    client.query({
      query: GET_QUERY,
      variables: { param: { type: 'Axxion', isin: content?.isin, mode } },
    });
  const [automatedColumns, setAutomatedColumns] = useState<any[]>([]);
  const [automaticTableData, setAutomaticTableData] = useState<any[]>([]);

  const fetchTable = async (type: string) =>
    client.query({
      query: GET_TABLE,
      variables: { param: { type, isin } },
    });

  const fetchAutomatedTable = async (type: string, fields: any[], limit: string) =>
    client.query({
      query: GET_AUTOMATED_TABLE,
      variables: { param: type, fields, limit },
    });

  const fetchAutomatedColumns = async (columns: any[]) =>
    client.query({
      query: GET_AUTOMATED_COLUMNS,
      variables: { param: { data: columns, isin: isin } },
    });

  const parseCell = (cell: any, rowIndex: number, colIndex: number) => {
    const data = automatedColumns.find((column) => column.columnIndex === colIndex)?.column[rowIndex];
    if (cell.fields?.some((field: any) => field.type === 'html')) {
      return <div dangerouslySetInnerHTML={{ __html: data }} />;
    }
    return data;
  };

  const generateAutomaticCell = (row: any, colIndex: number) => {
    return content?.body[0][colIndex]?.fields[0]?.valueType === 'date'
      ? moment(row[content?.body[0][colIndex]?.fields[0]?.field]).format('DD.MM.YYYY')
      : content?.body[0][colIndex]?.fields[0]?.valueType === 'percentage'
      ? `${row[content?.body[0][colIndex]?.fields[0]?.field]} %`
      : row[content?.body[0][colIndex]?.fields[0]?.field];
  };

  useEffect(() => {
    if (content?.title?.isDynamic) {
      fetchAutomatedColumns([content?.title]).then((res) => {
        setDynamicTitle(res.data?.getAutomatedColumns?.[0]?.[0]);
      });
    }
  }, [content?.title]);

  useEffect(() => {
    if (content?.type === 'Automatic') {
      const fields = content?.body[0].map(
        (item: any) =>
          item.fields && {
            field: item.fields[0]?.field,
            valueType: item.fields[0]?.valueType,
            isGroupBy: item.fields[0]?.isGroupBy,
            isSortBy: item.fields[0]?.isSortBy,
            order: item.fields[0]?.order,
          }
      );
      fetchAutomatedTable(
        content?.body[0][0]?.fields[0]?.table,
        fields,
        (content?.countOfRow ?? 'All').toString()
      ).then((res) => {
        const data = JSON.parse(res.data.getAutomaticTable.data) || [];

        setAutomaticTableData(
          data
            .map((tableRow: any) =>
              content.body[0].reduce((acc, item) => {
                if (Array.isArray(item.fields) && item.fields[0]?.field) {
                  acc[item.fields[0].field] = tableRow[item.fields[0].field];
                }
                return acc;
              }, {})
            )
            .filter((item: any) => Object.values(item).every((value) => !!value))
        );
      });
    }
  }, [content?.type]);

  useEffect(() => {
    if (content?.columns.some((column) => column.showValueConfiguration)) {
      const configColumns = content?.columns.reduce<any[]>((columns, column, index) => {
        if (column.showValueConfiguration) {
          return [...columns, content.body.map((row) => ({ ...row[index], columnIndex: index }))];
        }
        return columns;
      }, []);

      fetchAutomatedColumns(configColumns).then((res) => {
        if (res.data.getAutomatedColumns) {
          setAutomatedColumns(
            configColumns.map((column, colIndex) => {
              return {
                columnIndex: column[0].columnIndex,
                column: res.data.getAutomatedColumns[colIndex],
              };
            })
          );
        }
      });
    }
  }, [content]);

  useEffect(() => {
    setContent(originalContent);
  }, [originalContent]);

  useEffect(() => {
    if (content?.type === 'Axxion' && content?.isin) {
      fetchData('all').then((result) => {
        const data = result.data.getQuery.histories;
        const chartValues = [];
        const firstValue = data[0][1];
        const lastDate = moment(data[data.length - 1][0]);
        const lastYear = moment(data[data.length - 1][0]).subtract(1, 'year');
        const lastMonth = moment(data[data.length - 1][0]).subtract(1, 'month');
        const thisYear = moment(moment(data[data.length - 1][0]).format('YYYY'), 'YYYY');
        const threeYears = moment(data[data.length - 1][0]).subtract(3, 'year');
        let total = 0;
        const columns = ['Fonds'];

        for (let i = data.length - 1; i >= 0; i--) {
          const item = data[i];
          const [date, value] = item;
          const count = data.length - 1 - i;
          const pointValue = (total / count / firstValue - 1) * 100;

          if (moment(date).isBefore(lastMonth) && columns.length === 1) {
            columns.push(pointValue.toFixed(2).replace('.', ',') + '%');
          }

          if (moment(date).isBefore(thisYear) && columns.length === 2) {
            columns.push(pointValue.toFixed(2).replace('.', ',') + '%');
          }

          if (moment(date).isBefore(lastYear) && columns.length === 3) {
            columns.push(pointValue.toFixed(2).replace('.', ',') + '%');
          }

          if (moment(date).isBefore(threeYears) && columns.length === 4) {
            columns.push(pointValue.toFixed(2).replace('.', ',') + '%');
          }

          total += value;
        }
        setContent({
          ...content,
          body: [columns],
        });
      });
    } else if (content?.type === 'Preis') {
      fetchTable(content?.type).then((result) => {
        const dataString = result?.data?.getTable?.data;
        if (dataString) {
          setTableData(JSON.parse(dataString));
        }
      });
    }
  }, [content?.type]);

  const findRowSettings = (rowIndex: number) => {
    return content?.rowSetting?.find((row) => row.rowPos === rowIndex);
  };

  const handleLinkClick = (
    event: React.MouseEvent<HTMLAnchorElement>,
    cell: any
  ) => {
    if (cell?.rowConfirm?.enabled) {
      event.preventDefault();
      setPendingLinkUrl(cell?.linkUrl || null);
      setPendingRowConfirm(cell?.rowConfirm || null);
      setIsCancelAlert(false);
      setConfirmDialogOpen(true);
    }
  };

  const resetDialogState = () => {
    setConfirmDialogOpen(false);
    setTimeout(() => {
      setPendingLinkUrl(null);
      setPendingRowConfirm(null);
      setIsCancelAlert(false);
    }, 300);
  };

  const handleConfirm = () => {
    if (pendingLinkUrl) {
      resetDialogState();
      setTimeout(() => {
        window.open(pendingLinkUrl, '_blank', 'noopener,noreferrer');
      }, 300);
    }
  };

  const handleCancelClick = () => {
    if (pendingRowConfirm?.dialog?.showCancelAlert && !isCancelAlert) {
      setIsCancelAlert(true);
      return;
    }
    resetDialogState();
  };

  const handleClose = () => {
    resetDialogState();
  };

  return (
    <>
      {content?.title?.isDynamic ? (
        <div className="widget-title" style={{ color: color === '#ccd5df' ? '#003F7A' : color }}>
          {dynamicTitle}
        </div>
      ) : (
        <div className="widget-title" style={{ color: color === '#ccd5df' ? '#003F7A' : color }}>
          {content?.title}
        </div>
      )}
      <List className={`scroll-mobile ${content?.themeIndex === 0 ? 'current_positions_list' : ''} ${content?.columns?.length === 1 ? 'single-col' : ''}`}>
        {content?.showHeader && (
          <ListItem
            className={`list_heading_style_${content.themeIndex} ${content?.columns.length === 2 ? 'width-full' : ''}`}
          >
            {content?.header.map((cell, index, _array) =>
              content?.columns[index]?.type === 'avatar' ? (
                <ListItemAvatar style={{ width: '36px !important', ...(widgetTheme?.colorSet.tableText ? { color: widgetTheme?.colorSet.tableText } : {}) }} key={index}>
                  <span></span>
                </ListItemAvatar>
              ) : (
                <ListItemText
                  key={index}
                  style={{
                    width: `${+(1 / _array.length).toFixed(4) * 100}%`,
                    textAlign: index === 0 || content?.columns?.length === 2 ? 'left' : 'right',
                    ...(widgetTheme?.colorSet.tableText ? { color: widgetTheme?.colorSet.tableText } : {})
                  }}
                  primaryTypographyProps={{
                    sx: { fontSize: isMobile ? '14px' : undefined, hyphens: isMobile ? 'auto' : undefined },
                  }}
                >
                  {content?.columns[index]?.showColumnTooltip ? (
                    <div className="Tooltip_wrapper">
                      {cell}
                      <div className="Tooltip">
                        <InfoSharpIcon />
                        <div className="Tooltip-content">{content.columns[index]?.columnTooltip}</div>
                      </div>
                    </div>
                  ) : (
                    cell
                  )}
                </ListItemText>
              )
            )}
          </ListItem>
        )}
        {content?.type !== 'Automatic'
          ? content?.body?.map((row, index) => (
              <ListItem
                key={index}
                className={`list_content_style_${content.themeIndex} ${
                  content?.columns.length === 2 ? 'width-full' : ''
                }`}
                sx={{
                  fontWeight: findRowSettings(index)?.fontWeight,
                  color: findRowSettings(index)?.fontColor,
                  backgroundColor: findRowSettings(index)?.backgroundColor,
                  '& div, & span': {
                    fontWeight: findRowSettings(index)?.fontWeight,
                    color: findRowSettings(index)?.fontColor,
                    backgroundColor: findRowSettings(index)?.backgroundColor,
                    ...(widgetTheme?.colorSet.tableText ? { color: widgetTheme?.colorSet.tableText } : {})
                  },
                  ...(widgetTheme?.colorSet.tableText ? { color: widgetTheme?.colorSet.tableText } : {})
                }}
              >
                {row.map((cell: any, columnIndex, _array) =>
                  content?.columns[columnIndex]?.type === 'avatar' ? (
                    <ListItemAvatar key={index} sx={{ justifyContent: 'left !important', width: '36px !important' }}>
                      <img src={cell} style={{ width: 36, height: 36 }} alt="cell-avatar" />
                    </ListItemAvatar>
                  ) : (
                    <ListItemText
                      key={index}
                      style={{
                        width: `${+(1 / _array.length).toFixed(4) * 100}%`,
                        textAlign: columnIndex === 0 || content?.columns?.length === 2 ? 'left' : 'right',
                        ...(widgetTheme?.colorSet.tableText ? { color: widgetTheme?.colorSet.tableText } : {})
                      }}
                      primaryTypographyProps={{
                        sx: { fontSize: isMobile ? '14px' : undefined, hyphens: isMobile ? 'auto' : undefined },
                      }}
                    >
                      {cell?.linkUrl !== undefined ? (
                        <CustomStyledLink
                          target="_blank"
                          color={color}
                          className={'list-link-text'}
                          href={cell?.linkUrl}
                          onClick={(e) => handleLinkClick(e, cell)}
                        >
                          {cell?.linkText}
                        </CustomStyledLink>
                      ) : content?.columns[columnIndex]?.showValueConfiguration ? (
                        parseCell(cell, index, columnIndex)
                      ) : typeof cell === 'string' ? (
                        cell
                      ) : (
                        cell.text
                      )}
                    </ListItemText>
                  )
                )}
              </ListItem>
            ))
          : automaticTableData.map((row, rowIndex) => (
              <ListItem
                key={rowIndex}
                className={`list_content_style_${content.themeIndex} ${
                  content?.columns.length === 2 ? 'width-full' : ''
                }`}
                sx={{
                  fontWeight: findRowSettings(rowIndex)?.fontWeight,
                  color: findRowSettings(rowIndex)?.fontColor,
                  backgroundColor: findRowSettings(rowIndex)?.backgroundColor,
                  ...(widgetTheme?.colorSet.tableText ? { color: widgetTheme?.colorSet.tableText } : {}),
                  '& div, & span': {
                    fontWeight: findRowSettings(rowIndex)?.fontWeight,
                    color: findRowSettings(rowIndex)?.fontColor,
                    backgroundColor: findRowSettings(rowIndex)?.backgroundColor,
                    ...(widgetTheme?.colorSet.tableText ? { color: widgetTheme?.colorSet.tableText } : {})
                  }
                }}
              >
                {content?.columns?.map((cell: any, columnIndex: number, _array: any) => (
                  <ListItemText
                    key={columnIndex}
                    sx={{
                      width: `${+(1 / _array.length).toFixed(4) * 100}%`,
                      textAlign: columnIndex === 0 || content?.columns?.length === 2 ? 'left' : 'right',
                      ...(widgetTheme?.colorSet.tableText ? { color: widgetTheme?.colorSet.tableText } : {}),
                    }}
                    primaryTypographyProps={{
                      sx: { fontSize: isMobile ? '14px' : undefined, hyphens: isMobile ? 'auto' : undefined },
                    }}
                  >
                    {generateAutomaticCell(row, columnIndex)}
                  </ListItemText>
                ))}
              </ListItem>
            ))}
      </List>
      <Dialog sx={{ '.MuiPaper-root': { width: '100%', m: 0 } }} open={confirmDialogOpen} onClose={handleClose} fullWidth maxWidth="sm">
        <DialogTitle sx={{ textAlign: 'center', pt: 24, position: 'relative' }}>
          {isCancelAlert
            ? pendingRowConfirm?.dialog?.cancelAlertTitle
            : pendingRowConfirm?.dialog?.title}
          <IconButton
            aria-label="close"
            onClick={handleClose}
            sx={{ position: 'absolute', right: 8, top: 8, color: widgetTheme?.mainColor || color }}
          >
            <CloseIcon />
          </IconButton>
        </DialogTitle>
        <DialogContent>
          <Typography variant="body2" textAlign="center">
            {isCancelAlert
              ? pendingRowConfirm?.dialog?.cancelAlertText
              : pendingRowConfirm?.dialog?.text}
          </Typography>
        </DialogContent>
        {!isCancelAlert && (
          <DialogActions
            sx={{
              justifyContent: 'space-between',
              px: 24,
              pb: 24,
              gap: 16,
              display: 'flex',
              flexDirection: {
                sm: 'row',
                xs: 'column',
              }
            }}
          >
            <Button
              sx={{
                width: '100%',
                flex: 1,
                background: widgetTheme?.mainColor || color,
                border: '1px solid transparent',
                '&:hover': {
                  background: '#ffffff !important',
                  color: `${(widgetTheme?.mainColor || color)} !important`,
                  border: `1px solid ${(widgetTheme?.mainColor || color)} !important`,
                },
              }}
              onClick={handleConfirm}
              variant="contained"
            >
              {pendingRowConfirm?.dialog?.confirmText || 'Bestätigen'}
            </Button>
            <Button
              sx={{
                width: '100%',
                flex: 1,
                background: widgetTheme?.mainColor || color,
                border: '1px solid transparent',
                ml: { xs: '0 !important' },
                '&:hover': {
                  background: '#ffffff !important',
                  color: `${(widgetTheme?.mainColor || color)} !important`,
                  border: `1px solid ${(widgetTheme?.mainColor || color)} !important`,
                },
              }}
              onClick={handleCancelClick}
              variant="contained"
            >
              {pendingRowConfirm?.dialog?.cancelText || 'Abbrechen'}
            </Button>
          </DialogActions>
        )}
      </Dialog>
    </>
  );
};
