// Dependencies
import React, { FC, useEffect, useState, useMemo } from 'react';
import { Box, Button, CircularProgress, Container, Stack, Typography, useMediaQuery, FormControl, InputLabel, Select, MenuItem, FormControlLabel, Switch } from '@mui/material';
import ApexCharts, { ApexOptions } from 'apexcharts';
import ReactApexChart from 'react-apexcharts';
import moment from 'moment';
import de from 'apexcharts/dist/locales/de.json';

// Client
import { client } from '../../../../App';

// Queries
import { GET_QUERY } from '../../../../queries/history';
import { GET_AUTOMATED_COLUMNS } from '../../../../queries/widgets';
import { ITheme } from '../../widgetBuilder';

// Interface
type ViewMode = '1 Monat' | '6 Monate' | '1 Jahr' | '3 Jahr' | '5 Jahr' | '10 Jahr' | 'YTD' | 'Gesamt';

// Chart view modes
const _viewModes: ViewMode[] = ['1 Monat', '6 Monate', '1 Jahr', '3 Jahr', '5 Jahr', '10 Jahr', 'YTD', 'Gesamt'];

const years = Array(new Date().getFullYear() - 2007)
  .fill(2009)
  .map((y, index) => `${y + index}`);

type Props = {
  color: string;
  content?: {
    theme: string;
    chartType: string;
    isin?: string;
    title?: string | { isDynamic: boolean };
    chartTitle?: string;
    chartText?: string;
    chartTextStyle?: string;
    isPercent: boolean;
    additionalIsins?: any[];
    ranges?: string[];
  };
  isin?: string;
  theme?: ITheme;
};

export const SolActivePanel: FC<Props> = ({ color = '#003F7A', content, isin, theme }) => {
  // States
  const [currentViewMode, setCurrentViewMode] = useState<ViewMode>('Gesamt');
  const [dataForToday, setDataForToday] = useState<any[]>([]);
  const [dataForAll, setDataForAll] = useState<any[]>([]);
  const [data, setData] = useState<any[]>([]);
  const [additionalData, setAdditionalData] = useState<any[]>([]);
  const [viewModes, setViewModes] = useState(_viewModes);
  const isOldTheme = color === '#003F7A';
  const [loading, setLoading] = useState(false);
  const [firstChartValue, setFirstChartValue] = useState();
  const [dynamicTitle, setDynamicTitle] = useState('');
  const [isPercent, setIsPercent] = useState<boolean>(!!content?.isPercent);
  const isMobile = useMediaQuery('(max-width:600px)');

  // Chart option
  const options: ApexOptions = {
    chart: {
      id: 'area-datetime',
      type: 'area',
      height: 350,
      zoom: {
        autoScaleYaxis: true,
      },
      locales: [de],
      defaultLocale: 'de',
    },
    animations: {
      enabled: false,
    },
    legend: {
      show: true,
      showForSingleSeries: true,
      showForNullSeries: true,
      showForZeroSeries: true,
      position: 'bottom',
      containerMargin: {
        top: 16,
      },
      itemMargin: {
        vertical: 16        
      },
    },
    annotations: {
      xaxis: [
        {
          borderColor: color,
          // eslint-disable-next-line @typescript-eslint/ban-ts-comment
          // @ts-ignore
          tooltip: {
            enabled: false,
          },
          label: {
            text: 'Rally',
            style: {
              color: '#fff',
              background: color,
            },
          },
        },
      ],
    },
    dataLabels: {
      enabled: false,
    },
    markers: {
      size: 0,
    },
    xaxis: {
      type: 'datetime',
      tickAmount: 6,
    },
    yaxis: {
      labels: {
        formatter: (value: number): string => {
          if (value === 0) return `0${isPercent ? '%' : ''}`;
          if (isPercent) {
            const fixed = value.toFixed(2);
            // Remove trailing .00 if present, else keep up to 2 decimals
            const formatted = fixed.endsWith('.00')
              ? parseInt(fixed, 10).toString()
              : fixed.replace(/\.?0+$/, '').replace(/(\.\d*[1-9])0+$/, '$1');
            return `${formatted}%`;
          }
          return value.toString();
        },
      },
    },
    tooltip: {
      x: {
        format: 'MMM dd yyyy',
      },
    },
    colors: [content?.theme || color],
    fill: isOldTheme
      ? {
          type: 'gradient',
          gradient: {
            shadeIntensity: 1,
            opacityFrom: 0.7,
            opacityTo: 0.9,
            stops: [0, 100],
          },
        }
      : {
          type: ['solid', 'solid'],
          opacity: [0.2, 0],
        },
  };

  // Data fetcher
  const fetchData = async (mode: string, chartIsin: string | undefined, chartType: string | undefined, until = '') =>
    client.query({
      query: GET_QUERY,
      variables: { param: { type: chartType || 'Axxion', isin: chartIsin || isin, mode, until } },
    });

  const fetchAutomatedColumns = async (columns: any[]) =>
    client.query({
      query: GET_AUTOMATED_COLUMNS,
      variables: { param: { data: columns, isin: isin } },
    });

  // Change view mode handler
  const handleChangeViewMode = (viewMode: ViewMode) => {
    switch (viewMode) {
      case '1 Monat':
        ApexCharts.exec('area-datetime', 'zoomX', +new Date() - 30 * 24 * 60 * 60 * 1000, +new Date());
        break;
      case '6 Monate':
        ApexCharts.exec('area-datetime', 'zoomX', moment().subtract(6, 'months').valueOf().toString(), +new Date());
        break;
      case '1 Jahr':
        ApexCharts.exec('area-datetime', 'zoomX', moment().subtract(1, 'years').valueOf().toString(), +new Date());
        break;
      case '3 Jahr':
        ApexCharts.exec('area-datetime', 'zoomX', moment().subtract(1, 'years').valueOf().toString(), +new Date());
        break;
      case '5 Jahr':
        ApexCharts.exec('area-datetime', 'zoomX', moment().subtract(1, 'years').valueOf().toString(), +new Date());
        break;
      case '10 Jahr':
        ApexCharts.exec('area-datetime', 'zoomX', moment().subtract(1, 'years').valueOf().toString(), +new Date());
        break;
      case 'YTD':
        ApexCharts.exec('area-datetime', 'zoomX', moment().startOf('year').valueOf().toString(), +new Date());
        break;
      case 'Gesamt':
        ApexCharts.exec(
          'area-datetime',
          'zoomX',
          moment().subtract(5, 'years').valueOf().toString(),
          moment().valueOf().toString()
        );
        break;
      default:
    }

    const now = moment().valueOf().toString();
    const dates: Record<ViewMode, any> = {
      ['1 Monat']: moment().subtract(1, 'months').valueOf().toString(),
      ['6 Monate']: moment().subtract(6, 'months').valueOf().toString(),
      ['1 Jahr']: moment().subtract(1, 'years').valueOf().toString(),
      ['3 Jahr']: moment().subtract(3, 'years').valueOf().toString(),
      ['5 Jahr']: moment().subtract(5, 'years').valueOf().toString(),
      ['10 Jahr']: moment().subtract(10, 'years').valueOf().toString(),
      ['YTD']: moment().startOf('year').valueOf().toString(),
      ['Gesamt']: moment().valueOf().toString(),
    };

    if (viewMode === 'Gesamt') {
      setData(dataForAll);
    } else {
      let data = dataForAll.filter((d) => d[0] >= +dates[viewMode] && d[0] <= +now);
      const adData = additionalData[0]?.data?.filter((d: any) => d[0] >= +dates[viewMode] && d[0] <= +now);

      if (viewMode === '1 Monat' && adData && adData.length && data[0][0] <= adData[0][0]) {
        data = data.filter((d) => d[0] >= adData[0][0]);
      }

      fetchData(viewMode, content?.isin, content?.chartType).then((result) => {
        let resData: any[] = [];
        let startIndex = 0;
        while (startIndex <= result.data.getQuery?.histories?.length) {
          const date = result.data.getQuery?.histories[startIndex][0];
          const latestDate = result.data.getQuery?.histories?.[result.data.getQuery?.histories.length - 1][0];

          let difference;
          if (viewMode === '1 Monat') {
            const startDate = moment(latestDate).subtract(1, 'months');
            difference = startDate.diff(moment(date), 'days');
            if (difference <= 0) {
              resData = result.data.getQuery?.histories.slice(
                startIndex > 0 ? startIndex - (difference === 0 ? 0 : 1) : 0
              );
              break;
            }
          }
          if (viewMode === '6 Monate') {
            const startDate = moment(latestDate).subtract(6, 'months');
            difference = startDate.diff(moment(date), 'days');
            if (difference <= 0) {
              resData = result.data.getQuery?.histories.slice(
                startIndex > 0 ? startIndex - (difference === 0 ? 0 : 1) : 0
              );
              break;
            }
          }
          if (viewMode === '1 Jahr') {
            const startDate = moment(latestDate).subtract(1, 'years');
            difference = startDate.diff(moment(date), 'days');
            if (difference <= 0) {
              resData = result.data.getQuery?.histories.slice(
                startIndex > 0 ? startIndex - (difference === 0 ? 0 : 1) : 0
              );
              break;
            }
          }
          if (viewMode === '3 Jahr') {
            const startDate = moment(latestDate).subtract(3, 'years');
            difference = startDate.diff(moment(date), 'days');
            if (difference <= 0) {
              resData = result.data.getQuery?.histories.slice(
                startIndex > 0 ? startIndex - (difference === 0 ? 0 : 1) : 0
              );
              break;
            }
          }
          if (viewMode === '5 Jahr') {
            const startDate = moment(latestDate).subtract(5, 'years');
            difference = startDate.diff(moment(date), 'days');
            if (difference <= 0) {
              resData = result.data.getQuery?.histories.slice(
                startIndex > 0 ? startIndex - (difference === 0 ? 0 : 1) : 0
              );
              break;
            }
          }
          if (viewMode === '10 Jahr') {
            const startDate = moment(latestDate).subtract(10, 'years');
            difference = startDate.diff(moment(date), 'days');
            if (difference <= 0) {
              resData = result.data.getQuery?.histories.slice(
                startIndex > 0 ? startIndex - (difference === 0 ? 0 : 1) : 0
              );
              break;
            }
          }
          if (viewMode === 'YTD') {
            const startDate = moment(latestDate).startOf('year');
            difference = startDate.diff(moment(date), 'days');
            if (difference <= 0) {
              resData = result.data.getQuery?.histories.slice(
                startIndex > 0 ? startIndex - (difference === 0 ? 0 : 1) : 0
              );
              break;
            }
          }

          startIndex++;
        }

        setData((old) => [...old, ...resData].sort((a, b) => +new Date(a[0]) - +new Date(b[0])));
      });
      content?.additionalIsins?.forEach(async (additionalIsin) => {
        const chartIsin = additionalIsin?.isin;
        if (chartIsin) {
          return new Promise((resolve, reject) => {
            fetchData(viewMode, chartIsin, additionalIsin?.chartType || 'SolActive')
              .then((result) => {
                let resData: any[] = [];
                let startIndex = 0;
                const latestDate = result.data.getQuery?.histories?.[result.data.getQuery?.histories.length - 1][0];

                if ((viewMode as ViewMode) === 'Gesamt') {
                  resData = result.data.getQuery?.histories;
                } else {
                  while (startIndex <= result.data.getQuery?.histories?.length) {
                    const date = result.data.getQuery?.histories[startIndex][0];
                    let difference;
                    if (viewMode === '1 Monat') {
                      const startDate = moment(latestDate).subtract(1, 'months');
                      difference = startDate.diff(moment(date), 'days');
                      if (difference <= 0) {
                        resData = result.data.getQuery?.histories.slice(
                          startIndex > 0 ? (difference === 0 ? 0 : 1) : 0
                        );
                        break;
                      }
                    }
                    if (viewMode === '6 Monate') {
                      const startDate = moment(latestDate).subtract(6, 'months');
                      difference = startDate.diff(moment(date), 'days');
                      if (difference <= 0) {
                        resData = result.data.getQuery?.histories.slice(
                          startIndex > 0 ? (difference === 0 ? 0 : 1) : 0
                        );
                        break;
                      }
                    }
                    if (viewMode === '1 Jahr') {
                      const startDate = moment(latestDate).subtract(1, 'years');
                      difference = startDate.diff(moment(date), 'days');
                      if (difference <= 0) {
                        resData = result.data.getQuery?.histories.slice(
                          startIndex > 0 ? (difference === 0 ? 0 : 1) : 0
                        );
                        break;
                      }
                    }
                    if (viewMode === '3 Jahr') {
                      const startDate = moment(latestDate).subtract(3, 'years');
                      difference = startDate.diff(moment(date), 'days');
                      if (difference <= 0) {
                        resData = result.data.getQuery?.histories.slice(
                          startIndex > 0 ? (difference === 0 ? 0 : 1) : 0
                        );
                        break;
                      }
                    }
                    if (viewMode === '5 Jahr') {
                      const startDate = moment(latestDate).subtract(5, 'years');
                      difference = startDate.diff(moment(date), 'days');
                      if (difference <= 0) {
                        resData = result.data.getQuery?.histories.slice(
                          startIndex > 0 ? (difference === 0 ? 0 : 1) : 0
                        );
                        break;
                      }
                    }
                    if (viewMode === '10 Jahr') {
                      const startDate = moment(latestDate).subtract(10, 'years');
                      difference = startDate.diff(moment(date), 'days');
                      if (difference <= 0) {
                        resData = result.data.getQuery?.histories.slice(
                          startIndex > 0 ? (difference === 0 ? 0 : 1) : 0
                        );
                        break;
                      }
                    }
                    if (viewMode === 'YTD') {
                      const startDate = moment(latestDate).startOf('year');
                      difference = startDate.diff(moment(date), 'days');
                      if (difference <= 0) {
                        resData = result.data.getQuery?.histories.slice(
                          startIndex > 0 ? (difference === 0 ? 0 : 1) : 0
                        );
                        break;
                      }
                    }

                    startIndex++;
                  }
                }

                setAdditionalData((old) => {
                  if (old.some((item) => item.id === additionalIsin.id)) {
                    return old.map((item) =>
                      item.id === additionalIsin.id
                        ? {
                            id: additionalIsin.id,
                            name: additionalIsin.title,
                            data: [...(item.data || []), ...resData].sort((a, b) => +new Date(a[0]) - +new Date(b[0])),
                            color: '#545454',
                          }
                        : item
                    );
                  } else {
                    return [
                      ...old,
                      {
                        id: additionalIsin.id,
                        name: additionalIsin.title,
                        data: resData,
                        color: '#545454',
                      },
                    ];
                  }
                });
              })
              .catch((e) => reject(e));
          });
        }
      });
      setData(data);
    }

    setCurrentViewMode(viewMode);
  };

  useEffect(() => {
    if (content?.isin || isin) {
      fetchData('today', content?.isin, content?.chartType).then((result) => {
        setDataForToday(result.data.getQuery.histories);
      });
    }
  }, [isin, content?.isin]);

  useEffect(() => {
    if (content?.title && typeof content.title === 'object' && content.title.isDynamic) {
      fetchAutomatedColumns([content?.title]).then((res) => {
        setDynamicTitle(res.data?.getAutomatedColumns?.[0]?.[0]);
      });
    }
  }, [content?.title]);

  // On mounted
  useEffect(() => {
    (async () => {
      setLoading(true);
      setDataForAll([]);
      setData([]);
      if (content?.isin || isin) {
        await Promise.all(
          years.map(async (year, index) => {
            return new Promise((resolve, reject) => {
              fetchData('all', content?.isin, content?.chartType, year)
                .then((result) => {
                  setDataForAll((old) =>
                    [...old, ...result.data.getQuery.histories].sort((a, b) => +new Date(a[0]) - +new Date(b[0]))
                  );
                  setData((old) =>
                    [...old, ...result.data.getQuery.histories].sort((a, b) => +new Date(a[0]) - +new Date(b[0]))
                  );
                  resolve(1);
                })
                .catch((e) => reject(e));
            });
          })
        );
      }
      setLoading(false);
    })();
  }, [isin, content?.isin]);

  useEffect(() => {
    if (content?.additionalIsins?.length) {
      content?.additionalIsins.forEach(async (additionalIsin) => {
        const chartIsin = additionalIsin?.isin;
        if (chartIsin) {
          await Promise.all(
            years.map(async (year, index) => {
              return new Promise((resolve, reject) => {
                fetchData('all', chartIsin, additionalIsin?.chartType || 'SolActive', year)
                  .then((result) => {
                    const resData = result.data.getQuery?.histories || [[]];
                    setAdditionalData((old) => {
                      if (old.some((item) => item.id === additionalIsin.id)) {
                        return old.map((item) =>
                          item.id === additionalIsin.id
                            ? {
                                id: additionalIsin.id,
                                name: additionalIsin.title,
                                data: [...(item.data || []), ...resData].sort(
                                  (a, b) => +new Date(a[0]) - +new Date(b[0])
                                ),
                                color: '#545454',
                              }
                            : item
                        );
                      } else {
                        return [
                          ...old,
                          {
                            id: additionalIsin.id,
                            name: additionalIsin.title,
                            data: resData,
                            color: '#545454',
                          },
                        ];
                      }
                    });
                    resolve(1);
                  })
                  .catch((e) => reject(e));
              });
            })
          );
        }
      });
    }
  }, [content]);

  const chartData = useMemo(() => {
    if (data.length && isPercent) {
      const firstValue = data[0][1];
      setFirstChartValue(data[0]);
      return data.map((item) => [item[0], (item[1] / firstValue - 1) * 100]);
    }
    return data;
  }, [data, isPercent]);

  const additionalChartData = useMemo(() => {
    if (firstChartValue && isPercent) {
      return additionalData.map((lineData) => {
        const series = lineData.data;
        let lineSeries;
        if (!series.length) {
          lineSeries = [];
        } else {
          const index = series.findIndex((item: number[]) => item[0] > firstChartValue[0]);
          lineSeries = series.slice(index);
          if (series[index - 1]) {
            lineSeries.unshift([firstChartValue[0], series[index - 1][1]]);
          }
        }

        return {
          ...lineData,
          data: lineSeries
            .map((item: number[], index: number, _array: number[][]) => [item[0], (item[1] / _array[0][1] - 1) * 100])
            .sort((a: number[], b: number[]) => +new Date(a[0]) - +new Date(b[0])),
        };
      });
    }
    return additionalData;
  }, [additionalData, firstChartValue, isPercent]);

  // Return wicklung panel
  return (
    <Box
      position="relative"
      sx={{
        ...(theme?.colorSet?.tableText && {
          '& .apexcharts-legend-text' : {
            color: `${theme?.colorSet?.tableText} !important`
          }
        })
      }}
    >
      {loading && (
        <Box className={`chart-loader ${color}`}>
          <CircularProgress sx={{ color: color }} />
        </Box>
      )}

      {(typeof content?.title === 'string' || (typeof content?.title === 'object' && content?.title?.isDynamic && dynamicTitle)) && (
        <div className="widget-title" style={{ color: color === '#ccd5df' ? '#003F7A' : color }}>
          {typeof content.title === 'object' && content.title.isDynamic 
            ? dynamicTitle 
            : typeof content.title === 'string' 
              ? content.title 
              : ''}
        </div>
      )}

      {isMobile ? (
        <Box display="flex" justifyContent="center" mb={8} position="relative">
          <Box sx={{ width: '100%', display: 'flex', alignItems: 'center', gap: 16, position: 'relative' }}>
            <Typography sx={{ position: 'absolute', left: '4px', top: '-8px', zIndex: 99 }} id="solactive-view-mode-label">
              Zeitraum:
            </Typography>
            <Select
              sx={{ flex: 1, color: color || '#003F7A', 'padding': '0', '&:after': { borderBottomColor: color }, 'textAlign': 'right' }}
              size="small"
              variant="standard"
              labelId="solactive-view-mode-label"
              label="Zeitraum"
              value={currentViewMode}
              onChange={(e) => handleChangeViewMode(e.target.value as ViewMode)}
            >
              {viewModes
                .filter((mode) => {
                  if (!content?.ranges) return true;
                  return content?.ranges.includes(mode);
                })
                .map((viewMode) => (
                  <MenuItem sx={{ color: color || '#003F7A', borderBottom: '0' }} key={viewMode} value={viewMode}>
                    {viewMode}
                  </MenuItem>
                ))}
            </Select>
            {/* <FormControlLabel
              sx={{ position: 'absolute', left: '16px', top: '24px', zIndex: 99 }}
              label="in %"
              control={(
                <Switch
                  sx={{
                    '& .MuiSwitch-thumb': {
                      color: !isPercent ? '#fff' : color || '#003F7A',
                      padding: 0,
                    },
                    '& .MuiSwitch-track': {
                      backgroundColor: `${content?.theme || color || '#003F7A'} !important`,
                    },
                  }}
                  size="small"
                  checked={isPercent}
                  onChange={(e) => setIsPercent(e.target.checked)}
                />
              )}
            /> */}
          </Box>
        </Box>
      ) : (
        <Stack spacing={2} direction="row" alignItems="center" justifyContent="center">
          {/* <Box sx={{ flex: 1 }} /> */}
          <Box sx={{ display: 'flex', gap: 2 }}>
            {viewModes
              .filter((mode) => {
                if (!content?.ranges) {
                  return true;
                }

                return content?.ranges.includes(mode);
              })
              .map((viewMode, index) => (
                <Button
                  key={index}
                  sx={{
                    backgroundColor: viewMode === currentViewMode ? content?.theme || color || '#003F7A' : 'inherit',
                    color: viewMode === currentViewMode ? 'white' : content?.theme || color || '#003F7A',
                  }}
                  onClick={() => handleChangeViewMode(viewMode)}
                  variant={viewMode === currentViewMode ? 'contained' : 'text'}
                >
                  {viewMode}
                </Button>
              ))}
          </Box>
          {/* <Box sx={{ flex: 1, display: 'flex', justifyContent: 'flex-end' }}>
            <FormControlLabel
              label="in %"
              control={(
                <Switch
                  sx={{
                    '& .MuiSwitch-thumb': {
                      color: !isPercent ? '#fff' : color || '#003F7A',
                      padding: 0,
                    },
                    '& .MuiSwitch-track': {
                      backgroundColor: `${content?.theme || color || '#003F7A'} !important`,
                    },
                  }}
                  size="small"
                  checked={isPercent}
                  onChange={(e) => setIsPercent(e.target.checked)}
                />
              )}
            />
          </Box> */}
        </Stack>
      )}
      <Box sx={{ marginLeft: '-16px', width: 'calc(100% + 24px)' }}>
        <ReactApexChart
          options={options}
          series={[
            // eslint-disable-next-line @typescript-eslint/ban-ts-comment
            // @ts-ignore
            {
              name: content?.chartTitle || 'Frankfurter UCITS-ETF - Modern Value',
              data: chartData,
            },
            ...(additionalChartData?.length ? additionalChartData : []),
          ]}
          type="area"
          height={350}
        />
      </Box>
      <Container maxWidth="md">
        {content?.chartText !== undefined ? (
          <Typography sx={{ textAlign: content.chartTextStyle || 'left', ...(theme?.colorSet?.tableText && { color: theme?.colorSet?.tableText }) }}>{content.chartText}</Typography>
        ) : (
          <Typography sx={{ ...(theme?.colorSet?.text && { color: theme?.colorSet?.text }) }}>
            Wertentwicklungen in der Vergangenheit sind kein verlässlicher Indikator für die künftige Wertentwicklung.
          </Typography>
        )}
      </Container>
    </Box>
  );
};
