export interface IChartItem {
  label: string;
  value: number;
}

interface IPortfolio {
  regionDate: string;
  branchenDate: string;
  marketDate: string;
  region: IChartItem[];
  branchen: IChartItem[];
  market: IChartItem[];
}

interface IPosition {
  isin: string;
  emittent: string;
  anteliamfv: string;
  datum: string;
}

// Export portfolio panel
export const PortfolioPanel: FC<{ apiKey: string }> = ({ apiKey }) => {
  // States
  const [collapseStatus, setCollapseStatus] = useState([false, false, false, false]);
  const [portfolio, setPortfolio] = useState<IPortfolio>();
  const [positions, setPositions] = useState<IPosition[]>([]);
  const [loading, setLoading] = useState(false);

  const fetchPortfolio = async (apiKey: string) =>
    client.query({
      query: GET_PORTFOLIO,
      variables: { customerId: apiKey },
    });

  const fetchCixonPortfolio = async () =>
    client.query({
      query: GET_CIXON_PORTFOLIO,
    });

  // Toggle collapse handler
  const handleToggleCollapse = (index: number) => {
    collapseStatus[index] = !collapseStatus[index];
    setCollapseStatus([...collapseStatus]);
  };

  useEffect(() => {
    setLoading(true);
    fetchPortfolio(apiKey)
      .then((res) => {
        const response = res?.data?.getPortfolio;
        if (response) {
          setPortfolio(response);
        }
      })
      .finally(() => {
        setLoading(false);
      });

    fetchCixonPortfolio()
      .then((res) => {
        const response = res?.data?.getCixonPortfolio;
        setPositions(response || []);
      })
  }, [apiKey]);

  // Return portfolio panel
  return (
    <>
      {menuList.map((menu, index) => (
        <React.Fragment key={index}>
          <MenuItem onClick={() => handleToggleCollapse(index)}>
          <div className='MenuItem_inner'>
            {menu}
            {collapseStatus[index] ? <KeyboardArrowUpIcon/> : <KeyboardArrowDownIcon/>}
            </div>
          </MenuItem>
          <Collapse in={collapseStatus[index]}>
            {index === 0 && (
              <>
                <RegionenChart data={portfolio?.region} loading={loading} />
                <Stack mt={5} spacing={5} divider={<Divider />}>
                  <Typography>Diese Werte können sich im Laufe der Zeit ändern.</Typography>
                  <Typography>Quelle: Eigene Berechnungen Shareholder Value Management AG. Stand der Daten: {portfolio?.regionDate ? moment(+portfolio?.regionDate).format('DD.MM.YYYY') : ''}</Typography>
                </Stack>
              </>
            )}
            {index === 1 && (
              <>
                <BranchenChart data={portfolio?.branchen} loading={loading} />
                <Stack mt={5} spacing={5} divider={<Divider />}>
                  <Typography>Diese Werte können sich im Laufe der Zeit ändern.</Typography>
                  <Typography>Quelle: Eigene Berechnungen Shareholder Value Management AG. Stand der Daten: {portfolio?.branchenDate ? moment(+portfolio?.branchenDate).format('DD.MM.YYYY') : ''}</Typography>
                </Stack>
              </>
            )}
            {index === 2 && (
              <>
                <MarktkapitalisierungChart data={portfolio?.market} loading={loading} />
                <Stack mt={5} spacing={5} divider={<Divider />}>Gewicht im Portfolio
                  <Typography>Diese Werte können sich im Laufe der Zeit ändern.</Typography>
                  <Typography>Quelle: Eigene Berechnungen Shareholder Value Management AG. Stand der Daten: {portfolio?.marketDate ? moment(+portfolio?.marketDate).format('DD.MM.YYYY') : ''}</Typography>
                </Stack>
              </>
            )}
            {index === 3 && (
              <>
                <List className='scroll-mobile current_positions_list'>
                  <ListItem className='list_heading_style' key={index}>
                    <ListItemText className='title'>{`Unternehmen`}</ListItemText>
                    <ListItemText className='isin'>{`ISIN`}</ListItemText>
                    <ListItemText className='portfolio'>{`Gewicht im Portfolio`}</ListItemText>
                  </ListItem>
                  {
                    positions && positions.map(({ emittent: title, isin, anteliamfv: portfolio }, index) => {
                      return (
                        <ListItem key={index}>
                          <ListItemText className='title'>{title}</ListItemText>
                          <ListItemText className='isin'>{isin}</ListItemText>
                          <ListItemText className='portfolio'>{`${replaceComma(portfolio)}%`}</ListItemText>
                        </ListItem>
                      )
                    })
                  }
                </List>
                <Stack mt={5} spacing={5} divider={<Divider />}>
                  <Typography>Diese Werte können sich im Laufe der Zeit ändern.</Typography>
                  <Typography>Quelle: Eigene Berechnungen Shareholder Value Management AG. Stand der Daten: {positions ? moment(+positions[0]?.datum).format('DD.MM.YYYY') : ''}</Typography>
                </Stack>
              </>
            )}
          </Collapse>
        </React.Fragment>
      ))}
    </>
  );
};

