import { FC, useEffect, useState } from 'react';
import ReactApexChart from 'react-apexcharts';
import { ApexOptions } from 'apexcharts';
import { client } from '../../../App';
import { GET_QUERY } from '../../../queries/history';
import moment from 'moment';
import { GET_AUTOMATED_BAR_CHART, GET_AUTOMATED_COLUMNS } from '../../../queries/widgets';
import { ITheme } from './../widgetBuilder';
import { Box, useMediaQuery } from '@mui/material';

type Props = {
  color: string;
  content: {
    title?: string | { isDynamic: boolean };
    chartData: {
      name: string;
      data: number[];
    }[];
    categories: string[];
    isPercent: boolean;
    chartType: string;
    isin: string;
    color: string;
    automationType?: string;
    legend?: Record<string, string>;
  };
  theme?: ITheme;
};

export const BarChart: FC<Props> = ({ content, color, theme }) => {
  const [periodsData, setPeriodsData] = useState<any[]>([[]]);
  const [periodLabels, setPeriodLabels] = useState<string[]>();
  const [dynamicTitle, setDynamicTitle] = useState('');
  const isMobile = useMediaQuery('(max-width:680px)');
  const isTablet = useMediaQuery('(max-width:992px)');
  const isLaptop = useMediaQuery('(max-width:1280px)');

  const series = content?.isPercent
    ? content?.chartData.map((item) => {
        const firstValue = item.data[0];
        return {
          name: item.name,
          data: item.data.map((bar) => (bar / firstValue - 1) * 100),
        };
      }) || []
    : content?.chartData || [];
  const categories = content?.categories || [];

  // Compute an adaptive column width. When there are very few categories (1–2),
  // make the column cluster narrower so bars don't look excessively wide.
  // Determine how many categories (x-axis slots) we have.
  // For Automated charts, this should equal the item count of one field/series.
  const automatedSeriesLength = Array.isArray(periodsData?.[0]?.data)
    ? periodsData[0].data.length
    : 0;
  const labelCount = content?.chartType === 'Automated'
    ? (automatedSeriesLength || (periodLabels?.length || 0))
    : (content?.chartType !== 'Manuall' ? (periodLabels?.length || 0) : categories.length);
  const baseColumnWidthFraction = isMobile ? 0.9 : isTablet ? 0.8 : isLaptop ? 0.7 : 0.5;
  let columnWidthFraction = baseColumnWidthFraction;
  if (labelCount === 1) columnWidthFraction = Math.min(baseColumnWidthFraction, isMobile ? 0.3 : isTablet ? 0.2 : isLaptop ? 0.1 : 0.1);
  else if (labelCount === 2) columnWidthFraction = Math.min(baseColumnWidthFraction, isMobile ? 0.35 : isTablet ? 0.3 : 0.25);
  const columnWidthString = `${Math.round(columnWidthFraction * 100)}%`;

  const [annotationPoints, setAnnotationPoints] = useState<any[]>([]);
  const [hiddenLabelKeys, setHiddenLabelKeys] = useState<Set<string>>(new Set());

  function formatValue(value: number) {
    return `${value.toFixed(2)}${(content?.isPercent || content?.chartType === 'Automated') ? '%' : ''}`;
  }

  function computeSmallBarAnnotations(w: any) {
    // Guard: chart not yet ready
    if (!w || !w.globals) {
      if (annotationPoints.length) setAnnotationPoints([]);
      if (hiddenLabelKeys.size) setHiddenLabelKeys(new Set());
      return;
    }

    const gridHeight = Number(w?.globals?.gridHeight) || 0;
    const yAxisIndex = 0;
    const scale = w?.globals?.yAxisScale?.[yAxisIndex];
    const niceMinRaw = (scale && typeof scale.niceMin === 'number') ? scale.niceMin : (w?.globals?.minY ?? 0);
    const niceMaxRaw = (scale && typeof scale.niceMax === 'number') ? scale.niceMax : (w?.globals?.maxY ?? 0);
    const niceMin = Number.isFinite(niceMinRaw) ? Number(niceMinRaw) : 0;
    const niceMax = Number.isFinite(niceMaxRaw) ? Number(niceMaxRaw) : 0;
    const range = Math.max(1e-8, Math.abs(niceMax - niceMin));
    const baseline = niceMin > 0 ? niceMin : (niceMax < 0 ? niceMax : 0);

    const activeSeries = Array.isArray(content?.chartType !== 'Manuall' ? periodsData : series)
      ? (content?.chartType !== 'Manuall' ? periodsData : series)
      : [];
    const xLabels = (content?.chartType !== 'Manuall' ? (periodLabels || []) : categories) || [];

    // Only render external labels when not mobile and we actually have labels
    if (isMobile || !Array.isArray(activeSeries) || xLabels.length === 0) {
      if (annotationPoints.length) setAnnotationPoints([]);
      if (hiddenLabelKeys.size) setHiddenLabelKeys(new Set());
      return;
    }

    const points: any[] = [];
    const hiddenKeys: string[] = [];

    const totalSeries = Array.isArray(activeSeries) ? activeSeries.length : 0;
    if (totalSeries === 0) {
      if (annotationPoints.length) setAnnotationPoints([]);
      if (hiddenLabelKeys.size) setHiddenLabelKeys(new Set());
      return;
    }

    const slotWidth = xLabels.length ? (Number(w?.globals?.gridWidth) || 0) / xLabels.length : 0;
    const columnWidthPct = columnWidthFraction; // mirrors plotOptions.bar.columnWidth
    const clusterWidth = slotWidth * columnWidthPct;
    const barWidth = totalSeries > 0 ? clusterWidth / totalSeries : 0;
    const offsetFor = (sIndex: number) => (sIndex - (totalSeries - 1) / 2) * barWidth;

    activeSeries.forEach((s: any, seriesIndex: number) => {
      const sData: any[] = (s && Array.isArray(s.data)) ? s.data : [];
      const seriesColor = (w?.globals?.seriesColors && w.globals.seriesColors[seriesIndex])
        ? w.globals.seriesColors[seriesIndex]
        : (s?.color || theme?.colorSet?.text || '#111');
      sData.forEach((val: any, idx: number) => {
        const valueRaw = typeof val === 'number' ? val : Number(val);
        if (!Number.isFinite(valueRaw)) return;
        const value = valueRaw as number;
        const barHeightPx = Math.abs((value - baseline) / range) * gridHeight;
        if (barHeightPx < 12) {
          const cat = xLabels[idx];
          if (cat !== undefined) {
            const pad = (value >= baseline ? 0.01 : 0.02) * range; // a bit further for negatives
            const yForLabel = value >= baseline ? value + pad : value - pad;
            points.push({
              x: cat,
              y: yForLabel,
              xAxisIndex: 0,
              marker: { size: 0 },
              label: {
                text: formatValue(value),
                offsetY: 0,
                offsetX: offsetFor(seriesIndex),
                style: {
                  color: seriesColor,
                  background: 'transparent',
                  fontSize: '12px',
                  fontWeight: 700,
                },
                borderColor: 'transparent',
                borderWidth: 0,
                borderRadius: 0,
              },
            });
            hiddenKeys.push(`${seriesIndex}:${idx}`);
          }
        }
      });
    });

    const next = points;
    if (JSON.stringify(next) !== JSON.stringify(annotationPoints)) setAnnotationPoints(next);
    const prevKeysStr = Array.from(hiddenLabelKeys).join('|');
    const nextKeysStr = hiddenKeys.join('|');
    if (prevKeysStr !== nextKeysStr) setHiddenLabelKeys(new Set(hiddenKeys));
  }

  const fetchData = async (mode: string) =>
    client.query({
      query: GET_QUERY,
      variables: { param: { type: 'Axxion', isin: content?.isin, mode } },
    });

  const fetchBarChart = async () =>
    client.query({
      query: GET_AUTOMATED_BAR_CHART,
      variables: { isin: content?.isin, type: content?.automationType },
    });

  const fetchAutomatedColumns = async (columns: any[]) =>
    client.query({
      query: GET_AUTOMATED_COLUMNS,
      variables: { param: { data: columns, isin: content?.isin } },
    });

  useEffect(() => {
    if (content?.title && typeof content.title === 'object' && content.title.isDynamic) {
      fetchAutomatedColumns([content?.title]).then((res) => {
        setDynamicTitle(res.data?.getAutomatedColumns?.[0]?.[0]);
      });
    }
  }, [content?.title]);

  useEffect(() => {
    if (content?.chartType === 'Axxion' && content?.isin) {
      fetchData('all').then((result) => {
        const data = result.data.getQuery.histories;
        let year = 1;
        let totalForYears = 0;
        let total = 0;
        let count = 0;
        const chartValues = [];
        const periods = [];
        const firstValue = data[0][1];
        for (let i = data.length - 1; i >= 0; i--) {
          const item = data[i];
          const [date, value] = item;
          total += value;
          const lastDate = moment(data[data.length - 1][0]).subtract(year, 'year');
          if (moment(date).isBefore(lastDate)) {
            chartValues.push((totalForYears / count / firstValue - 1) * 100);
            year++;
            totalForYears = 0;
            count = 0;
            periods.push(`${lastDate.format('DD.MM.YYYY')} bis ${lastDate.add(1, 'year').format('DD.MM.YYYY')}`);
          } else {
            totalForYears += value;
            count++;
          }
        }
        chartValues.push((total / data.length / firstValue - 1) * 100);
        periods.push('Tag der Anlage');
        setPeriodsData([{ name: '', data: chartValues.reverse() }]);
        setPeriodLabels(periods.reverse());
      });
    }

    if (content?.chartType === 'Automated') {
      fetchBarChart().then((result) => {
        const data = result.data.getAutomatedBarChart;

        if (content?.automationType === 'all') {
          // Group data by type
          const groupedData = data.reduce((acc: any, item: any) => {
            if (!acc[item.type]) {
              acc[item.type] = [];
            }

            acc[item.type].push(item);
            return acc;
          }, {});

          const mainColors = [theme?.colorSet.primary, theme?.colorSet.secondary];

          const chartSeries = Object.keys(groupedData).map((type, index) => ({
            name: (content?.legend && content.legend[type])
              ? content.legend[type]
              : type.replace(/_/g, ' ').replace(/\b\w/g, (l) => l.toUpperCase()),
            data: groupedData[type].map((item: any) => item.value),
            color: Object.keys(groupedData).length === 2
              ? mainColors[index]
              : theme?.colorSet.gradientColors[index],
          }));

          const allPeriods: string[] = data.map((item: any) => {
            const toDate = moment(item.end, 'DD.MM.YYYY');
            const fromDate = moment(item.end, 'DD.MM.YYYY').subtract(1, 'year');
            const day = fromDate.day();
            if (day === 0) fromDate.subtract(2, 'day');
            else if (day === 6) fromDate.subtract(1, 'day');
            return `${fromDate.format('DD.MM.YYYY')} bis ${toDate.format('DD.MM.YYYY')}`;
          });

          // Remove duplicates and set labels
          const uniquePeriods = Array.from(new Set(allPeriods));
          setPeriodLabels(uniquePeriods);
          setPeriodsData(chartSeries);
        } else {
          setPeriodLabels(
            data.map((item: any) => {
              const toDate = moment(item.end, 'DD.MM.YYYY');
              const fromDate = moment(item.end, 'DD.MM.YYYY').subtract(1, 'year');
              const day = fromDate.day();
              if (day === 0) fromDate.subtract(2, 'day');
              else if (day === 6) fromDate.subtract(1, 'day');
              return `${fromDate.format('DD.MM.YYYY')} bis ${toDate.format('DD.MM.YYYY')}`;
            })
          );
          setPeriodsData([{ name: '', data: data.map((item: any) => item.value) }]);
        }
      });
    }
  }, [content?.chartType]);

  const options: ApexOptions = {
    chart: {
      type: 'bar',
      height: 350,
      events: {
        mounted: (_chartContext) => computeSmallBarAnnotations(_chartContext?.w),
        updated: (_chartContext) => computeSmallBarAnnotations(_chartContext?.w),
      },
    },
    legend: {
      show: content?.automationType === 'all',
      showForSingleSeries: true,
      showForNullSeries: true,
      showForZeroSeries: true,
      position: 'bottom',
      itemMargin: {
        vertical: 16
      },
    },
    plotOptions: {
      bar: {
        horizontal: false,
        columnWidth: columnWidthString,
        dataLabels: {
          hideOverflowingLabels: true,
          position: 'center',
        },
      },
    },
    dataLabels: {
      enabled: !isMobile,
      formatter: function (val, opts) {
        const value = typeof val === 'number' ? val : Number(val);
        const key = `${opts?.seriesIndex}:${opts?.dataPointIndex}`;
        if (hiddenLabelKeys.has(key)) return '';
        return formatValue(value);
      },
      offsetY: 0,
      style: {
        fontSize: '12px',
        fontWeight: 700,
        colors: ['#fff'],
      },
    },
    stroke: {
      show: true,
      width: 0,
      colors: ['#fe0f31'],
    },
    tooltip: {
      enabled: true,
      followCursor: true,
      custom: function ({ series, seriesIndex, dataPointIndex }) {
        return isMobile && content?.chartType === 'Automated' ? `
          <div class="custom-tooltip" style="border-color: ${color}; text-align: center;">
            ${periodLabels?.[dataPointIndex]}
            <br />
            <b>${series[seriesIndex][dataPointIndex]?.toFixed(2)}%</b>
          </div>
        ` : `
          <div class="custom-tooltip" style="border-color: ${color};">
            ${series[seriesIndex][dataPointIndex]?.toFixed(2)}%
          </div>
        `;
      },
    },
    states: {
      normal: {
        filter: { type: 'none', value: 0 },
      },
      hover: {
        filter: { type: 'none', value: 0 },
      },
      active: {
        filter: { type: 'none', value: 0 },
      },
    },
    xaxis: {
      categories: content?.chartType !== 'Manuall' ? periodLabels : categories,
      labels: {
        formatter: function (val) {
          if (!isMobile || typeof val !== 'string') return val;
          const parts = val.split(' bis ');
          if (parts.length !== 2) return val;
          const from = moment(parts[0], 'DD.MM.YYYY', true);
          const to = moment(parts[1], 'DD.MM.YYYY', true);
          if (from.isValid() && to.isValid()) return `${from.format('YYYY')}/${to.format('YYYY')}`;
          return val;
        },
      },
    },
    yaxis: {
      title: {
        text: '',
        style: {
          fontWeight: 500,
        },
      },
      labels: {
        formatter: function (val) {
          return `${Math.floor(val)}${(content?.isPercent || content?.chartType === 'Automated') ? '%' : ''}`;
        },
      },
    },
    fill: {
      opacity: 1,
    },
    colors: theme
      ? periodsData.length === 2
      ? [theme.colorSet.primary || color, theme.colorSet.secondary || color]
      : theme.colorSet.gradientColors
      : [content.color || color],
    annotations: {
      position: 'front',
      points: annotationPoints,
    },
  };

  return (
    <Box
      sx={{
        ...(theme?.colorSet?.tableText && {
          '& .apexcharts-legend-text' : {
            color: `${theme?.colorSet?.tableText} !important`
          }
        })
      }}
    >
      {(typeof content?.title === 'string' || (typeof content?.title === 'object' && content?.title?.isDynamic && dynamicTitle)) && (
        <div className="widget-title" style={{ color: color === '#ccd5df' ? '#003F7A' : color }}>
          {typeof content.title === 'object' && content.title.isDynamic
            ? dynamicTitle
            : typeof content.title === 'string'
              ? content.title
              : ''}
        </div>
      )}
      <Box sx={{ marginLeft: '-16px', width: 'calc(100% + 24px)' }}>
        <ReactApexChart
          type="bar"
          height={350}
          options={options}
          series={content?.chartType !== 'Manuall' ? periodsData : series}
        />
      </Box>
    </Box>
  );
};
